<template>
  <div id="users">
    <div class="table-box">
      <div class="table-title">
        用户管理
        <img src="../../assets/users/提交-常态@3x.png" @click="save">
      </div>
      <Table  border  :columns="columns" :data="content" :max-height="clientHeight * 0.78" :disabled-hover="true">
        <template slot-scope="{ row }" slot="controls">
          <img src="../../assets/users/1@3x.png" class="btn-del" v-show="user.id != row.id" @click="remove(row)">
        </template>
        <template slot-scope="{ row }" slot="loginName">
          <Input v-model="row.loginName"  @on-change="inputChange($event,row)"/>
        </template>
        <template slot-scope="{ row }" slot="password">
          <Input v-model="row.password"  @on-change="inputChange($event,row)"/>
        </template>
        <template slot-scope="{ row }" slot="role">
          <Select v-model="row.role" @on-change="selectChange($event,row)" :disabled="user.id == row.id">
            <Option v-for="(item,index) in roles" :value="item" :key="index">{{ item }}</Option>
          </Select>
        </template>
        <template slot-scope="{ row }" slot="organizationId">
          <Select v-model="row.organizationId" @on-change="selectChange($event,row)" :disabled="user.id == row.id">
            <Option v-for="(item,index) in organizations" :value="item.id" :key="index">{{ item['名称']?item['名称']:'未知名称' }}</Option>
          </Select>
        </template>
      </Table>
      <div class="append-box">
        <img @click.stop="appendKey()"  src="../../assets/sidebar/添加@2x.png">
      </div>
    </div>

  </div>
</template>

<script>
import DB from "@lib/Db";
import auth from "@lib/auth";

export default {
    components: {},

    props: {

    },

    data(){
        return{
            clientHeight:document.documentElement.clientHeight,

            columns:[
                {
                    type: 'index',
                    align: 'center',
                    title:'编号',
                    width:80
                },
                {
                    slot:'controls',
                    title:'操作',
                    width:80,
                    align: 'center',

                },{
                    slot:'loginName',
                    title:'用户名',
                    align: 'center',

                }
                ,{
                    slot:'password',
                    title:'密码',
                    align: 'center',

                },{
                    slot:'role',
                    title:'角色',
                    align: 'center',

                }
                ,{
                    slot:'organizationId',
                    title:'组织',
                    align: 'center',
                }
            ],
            content:[],
            roles:["管理员","操作员"],
            organizations:[],
            user:{
                id:0
            }
        }
    },

    watch: {},

    created () {},

    mounted () {
        this.user = auth.user
        let organization = DB.selectOne('组织','id='+this.user['组织id'])
        if(organization['类型'] == 0){
            this.organizations = DB.Select('组织','','')
            this.users = DB.Select("用户","","")
        }else{
            this.users = DB.Select("用户",'"组织id"='+organization.id,"")
            this.organizations = [organization]
        }
        let content = []
        this.users.forEach((item)=>{
            content.push({
                id:item.id,
                loginName:item["登录名"],
                role:item.role,
                password:item['密码'],
                organizationId:item["组织id"]
            })
        })
        this.content = content
    },

    methods: {
        remove(row){
            this.content.splice(row._index,1)
        },
        save(){
            let isHave = false
            let user
            this.content.forEach((item)=>{
                user = DB.selectOne('用户','"登录名" = \''+item.loginName+"'")
                if(!user||(user&&item.id==user.id)){
                    if(item.id){
                        DB.Update("用户",{
                            id:item.id,
                            "登录名":item.loginName,
                            role:item.role,
                            "密码":item.password,
                            "组织id":item.organizationId
                        })
                    }else{
                        item.id = DB.Insert("用户",{
                            "登录名":item.loginName,
                            role:item.role,
                            "密码":item.password,
                            "组织id":item.organizationId
                        })
                    }
                }else{
                    isHave = true
                }
            })
            this.users.forEach((item)=>{
                user = this.content.find((c)=>{
                    return c.id == item.id
                })
                if(!user){
                    DB.Remove('用户',item.id)
                }
            })
            if(isHave){
                this.$Message.error("用户名重复命名的，无法保存！")
            }else{
                this.$Message.success("保存成功！")
            }
        },
        selectChange(e,row){
            this.content[row._index] = row
        },
        inputChange(e,row){
            this.content[row._index] = row
        },
        appendKey(){
            this.content.push({
                loginName:"",
                password:"",
                role:"管理员",
                organizationId:this.organizations[0].id
            })
            this.$nextTick(()=>{
                let domList = document.getElementsByClassName("ivu-table-overflowY")
                if(domList.length){
                    domList[0].scrollTop = domList[0].scrollHeight
                }
            })

        }
    }
}
</script>

<style>
  #users {
    display: flex;
    justify-content: center;
  }
  #users .table-box{
    width: 95%;

  }
  #users .table-title{
    border:solid 1px #dcdee2;
    margin-top: 20px;
    height: 50px;
    line-height: 50px;
    border-bottom: none;
    text-align: center;
    background: rgb(225,241,249);
    position: relative;
  }
  #users .table-title img{
    position: absolute;
    right: 20px;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 30px;
  }
  #users .btn-del{
    width: 20px;
    display: block;
    margin: auto;
  }
  #users .append-box{
    border:solid 1px #dcdee2;
    border-top: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
  }
  #users .append-box img{
    height: 50%;
  }
  #users .ivu-table-wrapper{
    overflow: visible;
  }
</style>