<template>
  <div id="analysis">
    <div class="analysis-index" v-show="viewState<2">
      <Split v-model="split1" min="0" max="200px">
        <Sider slot="left"/>
        <div class="right-box" slot="right">
          <div class="top-box">
            <Split v-model="split2" min="0" max="200px">
              <div class="left-table" slot="left">
                <div v-show="viewState == 0" class="table-box">
                  <div class="table-title">
                    统计
                  </div>
                  <Table  border  :columns="table.count.columns" :data="table.count.content" >
                    <template slot-scope="{ row }" slot="state">
                      {{stateList.length}}个
                    </template>
                    <template slot-scope="{ row }" slot="batch">
                      <a @click="fastCompare('batch')">{{batchList.length}}批</a>
                    </template>
                    <template slot-scope="{ row }" slot="air">
                      <a @click="fastCompare('air')">{{airList.length}}架</a>
                    </template>
                    <template slot-scope="{ row }" slot="organization">
                      {{organizationList.length}}个
                    </template>
                    <template slot-scope="{ row }" slot="change">
                      <a @click="fastCompare('technical')">{{changeHistoryList.length}}次</a>
                    </template>
                  </Table>
                </div>
                <div v-show="viewState == 0 "class="table-box" v-if="tree[0]">
                  <div class="table-title">
                    分析条件
                  </div>
                  <Table  border :disabled-hover="true"  :columns="table.condition.columns" :data="table.condition.content" :max-height="clientHeight * 0.59">
                    <template slot-scope="{ row }" slot="control">
                      <img src="../../assets/users/1@3x.png" class="btn-del" @click="remove(row)">
                    </template>
                    <template slot-scope="{ row }" slot="condition">
                      <!--{{row.type}}{{row.typeName}}-->
                      <!--{{row.typeNo}}-->
                      <Select v-model="row.compareValue" @on-change="selectChange($event,row)">
                        <Option v-for="(item,index) in table.condition.selects" v-show="row.compareType == item.type" :value="item.value" :key="index">{{ item.value }}</Option>
                      </Select>
                    </template>
                    <template slot-scope="{ row }" slot="content">
                      <Input v-if="row.type==0&&row.typeNo!=8&&row.typeNo!=9" v-model="row.content"  :type="row.inputType" @on-change="inputChange($event,row)"/>
                      <input v-if="row.typeNo==8||row.typeNo==9" type="file">
                      <Select v-model="row.content" v-if="row.type==1" @on-change="selectChange($event,row)">
                        <Option v-for="(item,index) in row.selects" :value="item.no" :key="index">{{ item.value }}</Option>
                      </Select>
                    </template>
                  </Table>
                </div>
                <div v-show="viewState == 1" class="tables-box">
                  <div class="tables-item">
                    <div class="table-title">
                      批次
                    </div>
                    <Table  border @on-selection-change="batchTableSelect" :columns="table.batchList.columns" :data="table.batchList.content" :max-height="clientHeight * 0.78">
                    </Table>
                  </div>
                  <div class="tables-item">
                    <div class="table-title">
                      单机
                    </div>
                    <Table  border  @on-selection-change="airTableSelect" :columns="table.airList.columns" :data="table.airList.content" :max-height="clientHeight * 0.78">
                      <template slot-scope="{ row }" slot="state">
                        {{stateList.length}}个
                      </template>
                      <template slot-scope="{ row }" slot="batch">
                        {{batchList.length}}批
                      </template>
                      <template slot-scope="{ row }" slot="air">
                        {{airList.length}}架
                      </template>
                      <template slot-scope="{ row }" slot="organization">
                        {{organizationList.length}}个
                      </template>
                      <template slot-scope="{ row }" slot="change">
                        {{changeHistoryList.length}}次
                      </template>
                    </Table>
                  </div>
                </div>
              </div>
              <div class="right-tree" slot="right">

                <Tree ref="tree" :show-checkbox="true" empty-text="" @on-check-change="nodeCheck" :data="tree"  class="demo-tree-render"></Tree>


              </div>
            </Split>
          </div>
          <div class="bottom-box">
            <div class="control-box" v-show="viewState==0">
              <div class="btn"><Button @click="refresh">重置</Button></div>
              <div class="btn"><Button @click="search">检索</Button></div>
            </div>
            <div class="control-box" v-show="viewState==1">
              <div class="btn"><Button @click="viewDetail" :disabled="(table.airList.chooseList.length+table.batchList.chooseList.length) != 1">浏览</Button></div>
              <div class="btn"><Button @click="onCompare" :disabled="(table.airList.chooseList.length+table.batchList.chooseList.length) < 2">分析</Button></div>
              <div class="btn"><Button @click="refresh">重置</Button></div>
              <div class="btn"><Button @click="viewState = 0">返回</Button></div>
            </div>
          </div>
        </div>
      </Split>
    </div>
    <div class="analysis-compare" v-show="viewState == 2">

        <div class="top-box">
          <div class="check-box">
            <div class="left-box">
              <Checkbox v-model="compareChecks.highlightState">高亮显示不同项</Checkbox>
              <Checkbox v-model="compareChecks.sameHideState">相同项隐藏</Checkbox>
            </div>
            <div class="right-box">
              <!--<Checkbox-->
                      <!--:indeterminate="compareChecks.indeterminate"-->
                      <!--:value="compareChecks.checkAllState"-->
                      <!--@click.prevent.native="handleCheckAll">全选</Checkbox>-->
              <!--<CheckboxGroup v-model="compareChecks.checkList" @on-change="checkAllGroupChange">-->
                <!--<Checkbox :label="item.title" v-for="(item,index) in compareChecks.contentList" :key="index">{{item.title}}</Checkbox>-->
              <!--</CheckboxGroup>-->
              <RadioGroup v-model="compareChecks.radioTitle">
                <Radio :label="item.title" v-for="(item,index) in compareChecks.contentList" :key="index">{{item.title}}</Radio>
              </RadioGroup>
            </div>
          </div>
          <div class="table-box">
            <Table  border row-key="name" @on-cell-click="tableClick" :columns="table.compare.columns" :data="table.compare.viewList" :max-height="clientHeight * 0.7">
              <template slot-scope="{ row ,column}" v-for="(expandColumn) in table.compare.expandColumns"  :slot="expandColumn.slot">
                <div :class="[{'highlight':row.isNotSame&&compareChecks.highlightState},{'current':(table.compare.currentRow?row.name==table.compare.currentRow.name:false)&&(table.compare.currentColumn?column.slot==table.compare.currentColumn.slot:false)}]">
                  <Button v-if="row.typeNo == 8||row.typeNo == 9" @click="openFile(row[expandColumn.slot])">打开文件</Button>
                  <span v-else>{{row[expandColumn.slot]}}</span>
                </div>
                <Select v-model="expandColumn[row.tableName+'-index']" v-if="expandColumn[row.tableName+'-list']&&row[expandColumn.slot+'-header']" @on-change="listSelectChange($event,row,column)">
                  <Option v-for="(item,index) in expandColumn[row.tableName+'-list']" :value="index" :key="index">{{index}}{{ item['名称'] }}</Option>
                </Select>
              </template>
            </Table>
          </div>
        </div>
        <div class="bottom-box">
          <div class="control-box">
            <div class="btn"><Button @click="findRelated" :disabled="!table.compare.currentColumn||!table.compare.currentRow">找相关</Button></div>
            <div class="btn"><Button @click="refresh">重置</Button></div>
            <div class="btn"><Button @click="exitCompare">返回</Button></div>
          </div>
        </div>
    </div>
    <div class="analysis-compare" v-show="viewState == 3">
      <div class="top-box">
        <div class="compare-box">
          <div class="scroll-box">
            <div class="item-box" v-for="(item,itemIndex) in table.technicalCompare.content" :key="itemIndex">
              <div class="title-box">状态号：{{item.stateNo}}<br>{{item.type=='batch'?'批次':'单机'}}号：{{item.no}}</div>
              <div class="list-box">
                  <div class="technical-box" v-for="(technical,index) in item.content" :key="index" :class="item.currentIndex == index?'choose':''"  @click="chooseTechnical(itemIndex , index)">
                      {{technical.timeStr}}&nbsp;{{technical.name}}
                  </div>
              </div>
              <div class="detail-box" v-if="item.content[item.currentIndex]">
                  <p>文件类型：{{item.content[item.currentIndex].type}}</p>
                  <p>文件名称：{{item.content[item.currentIndex].name}}</p>
                  <p>文件数量：{{item.content[item.currentIndex].fileCount}}</p>
                  <p>文件版本：{{item.content[item.currentIndex].version}}</p>
                  <p>修订日期：{{item.content[item.currentIndex].timeStr}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="bottom-box">
        <div class="control-box">
          <div class="btn"><Button @click="viewState = 0">返回</Button></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import Sider from '@component/Sider'
    import Bus from '@event/bus';
    import DB from "@lib/Db";
export default {
    data() {
        return {
            split1: 0.15,
            split2: 0.8,
            viewState:0,//0首页  1检索结果页  2批次比较页 3技术状态比较页
            table:{
                count:{
                    columns:[
                        {
                            title:"状态",
                            slot:"state",
                            align:"center"
                        },{
                            title:"批次",
                            slot:"batch",
                            align:"center"
                        },{
                            title:"单机",
                            slot:"air",
                            align:"center"
                        },{
                            title:"接装单位",
                            slot:"organization",
                            align:"center"
                        },{
                            title:"技术状态变更",
                            slot:"change",
                            align:"center"
                        }
                    ],
                    content:[
                        {
                            state:0,
                            batch:0,
                            air:0,
                            organization:0,
                            change:0
                        }
                    ],

                },
                batchList:{
                    columns:[
                        {
                            type: 'selection',
                            align:'center',
                            width:60
                        },
                        {
                            key:'name',
                            title:"名称",
                            align:'center'
                        }
                    ],
                    content:[],
                    chooseList:[],
                },
                airList:{
                    columns:[ {
                        type: 'selection',
                        align:'center',
                        width:60
                    },
                        {
                            key:'name',
                            title:"名称",
                            align:'center'
                        }],
                    content:[],
                    chooseList:[],
                },
                condition:{
                    columns:[{
                        type:"index",
                        title:"序号",
                        width:70,
                        align:"center"
                    },{
                        slot:"control",
                        title:"操作",
                        width:70,
                        align:"center"
                    },{
                        key:"name",
                        title:"项目",
                    },{
                        slot:"condition",
                        title:"条件",
                        align:"center"
                    },{
                        slot:"content",
                        title:"内容",
                        align:"center"
                    }],
                    content:[],
                    selects:[ //type 0 字符串 1 数字  2枚举
                        {
                            type:0,
                            value:"=",
                        },
                        {
                            type:0,
                            value:"包含"
                        },
                        {
                            type:1,
                            value:"="
                        },{
                            type:1,
                            value:">"
                        },
                        {
                            type:1,
                            value:">="
                        },
                        {
                            type:1,
                            value:"<"
                        },
                        {
                            type:1,
                            value:"<="
                        },
                        {
                            type:2,
                            value:"="
                        },
                    ]
                },
                compare:{
                    columns:[],
                    currentColumn:null,
                    currentRow:null,
                    expandColumns:[],
                    content:[],
                    viewList:[],
                },
                technicalCompare:{
                    content:[]
                }
            },
            tree:[],
            templateId:1,
            stateList:[],
            batchList:[],
            airList:[],
            organizationList:[],
            changeHistoryList:[],
            compareChecks:{
                sameHideState:false,
                highlightState:false,
                indeterminate:false,
                checkList:[],
                checkAllState:false,
                radioTitle:"使命任务",
                contentList:[
                    {title:"使命任务",},
                    {title:"布局"},
                    {title:"主要技术参数"},
                    {title:"主要配备"},
                    {title:"构型"},
                    {title:"保障资源"},
                    {title:"喷涂方案"},
                    {title:"技术状态文件"},
                    {title:"有寿件"},
                    {title:"双流水"},
                    {title:"交付遗留问题及处置情况"}]
            },
            clientHeight:document.documentElement.clientHeight
        }
    },
    components: {
        Sider,
    },
    watch:{
        'compareChecks.checkList':function(){
            this.tableCompareContent()
        },
        'compareChecks.sameHideState':function(){
            this.tableCompareContent()
        },
        'compareChecks.highlightState':function(){
//            this.tableCompareContent()
        },
        'compareChecks.radioTitle':function(){
            this.tableCompareContent()
        }
    },
    beforeDestroy(){
        Bus.$off('analysis-check')
        Bus.$off('analysis-template-change')

    },
    mounted(){
        this.types = DB.Select("types","","")
        this.schemas = DB.Select("schemas","","")
        Bus.$on("analysis-check",(content)=>{
            this.stateList = content.stateList
            this.batchList = content.batchList
            this.airList = content.airList
            this.organizationList = content.organizationList
            this.changeHistoryList = []
            this.batchList.forEach((item)=>{
                this.changeHistoryList = this.changeHistoryList.concat(DB.Select("change_histories",'"基线id" = '+item.baselineId,""))
            })
            this.airList.forEach((item)=>{
                this.changeHistoryList = this.changeHistoryList.concat(DB.Select("change_histories",'"基线id" = '+item.baselineId,""))
            })
        })
        this.createTree()
        Bus.$on("analysis-template-change",(templateId)=>{
            this.templateId = templateId
            this.createTree()
            this.$nextTick(()=>{
                this.stateList = []
                this.batchList = []
                this.airList = []
                this.organizationList = []
                this.changeHistoryList = []
                this.viewState = 0
                this.$refs.tree.handleCheck({checked:false,nodeKey:this.tree[0].nodeKey})
            })
        })
    },
    
    activated(){
        console.log('缓存了')
    },
    methods:{
        openFile(fileId){
            DB.OpenFile(fileId)
        },
        exitCompare(){
            if(this.table.batchList.chooseList.length+this.table.airList.chooseList.length >1){
                this.viewState = 1
            }else{
                this.viewState = 0
            }
        },
        viewDetail(){
            let obj
            if(this.table.airList.chooseList.length){
                 obj = this.table.airList.chooseList[0]
                 obj.dataType = 'air'
            }
            if(this.table.batchList.chooseList.length){
                obj = this.table.batchList.chooseList[0]
                obj.dataType = 'batch'
            }
            this.$router.push({
                path:'/',
                query:{
                    id:obj.baselineId,
                    dataType:obj.dataType
                }
            })
        },
        chooseTechnical(itemIndex,index){
            this.$set(this.table.technicalCompare.content[itemIndex],'currentIndex',index)
//            this.table.technicalCompare.content[itemIndex].currentIndex = index
            this.$forceUpdate()
        },
        NumToStr(num){
            if(num<10){
                num = "0"+num
            }
            return num
        },
        DatetimeToStr(datetime){
            let date = new Date(Number(datetime))
            return date.getFullYear()+"-"+this.NumToStr((date.getMonth()+1))+"-"+this.NumToStr(date.getDate())+"  "+this.NumToStr(date.getHours())+":"+this.NumToStr(date.getMinutes())
        },
        fastCompare(type){//快速分析
            let list = []
            switch (type){
                case "batch":
                    this.batchList.forEach((item)=>{
                        list.push({
                            id:item.id,
                            baselineId:item.baselineId,
                            name:item.title,
                            batchId:item.batchId,
                            stateId:item.stateId
                        })
                    })
                    this.table.batchList.chooseList = this.objDeepCopy(list)
                    if(this.table.batchList.chooseList.length>1){
                        this.onCompare()
                    }
                    break;
                case "air":
                    this.airList.forEach((item)=>{
                        list.push({
                            id:item.id,
                            baselineId:item.baselineId,
                            name:item.title,
                            batchId:item.batchId,
                            stateId:item.stateId
                        })
                    })
                    this.table.airList.chooseList = this.objDeepCopy(list)
                    if(this.table.airList.chooseList.length>1){
                        this.onCompare()
                    }
                    break;
                case "technical":

                    if((this.airList.length + this.batchList.length)> 1&& this.changeHistoryList.length){
                        let content = []

                        this.airList.forEach((item)=>{
                            item.type = 'air'
                            let obj = DB.Select('飞机','id='+item.id,'')[0]
                            item.no = obj['编号']
                            content.push(item)
                        })
                        this.batchList.forEach((item)=>{
                            item.type = 'batch'
                            let obj = DB.Select('批次','id='+item.id,'')[0]
                            item.no = obj['编号']
                            content.push(item)
                        })
                        let listItem =   DB.selectOne("lists","parent_no="+DB.tableNos['技术状态文件'],"")
                        let technologyStateKeyList = DB.getSchemaComponents(this.templateId,listItem.element_type_no)
                        let technologyStateFileTypeList=[]
                        technologyStateKeyList.forEach((key)=>{
                            if(key.name == "类型"){
                                console.log(key)
                                technologyStateFileTypeList = DB.getCodeValues("1",key.type_no)
                            }
                        })
                        content.forEach((item)=>{

                            let state = DB.Select('状态','id='+item.stateId,'')[0]
                            item.stateNo = state['编号']
                            item.currentIndex = 0
                            item.content = this.changeHistoryList.filter((c)=>{
                                return c['基线id'] == item.baselineId
                            })

                            item.content.forEach((child)=>{
                                let file = DB.Select('技术状态文件','id = '+child.control_file_id,'')[0]
                                let changeHistoryDetails = DB.Select("change_history_details","change_history_id = "+child.id,"")
                                child.version = file['版本']
                                child.name = file['名称']
                                child.fileCount = changeHistoryDetails.length
                                child.type = (technologyStateFileTypeList.find((type)=>{
                                    return type.no == file['类型']
                                })['value'])
                                child.timeStr = this.DatetimeToStr(child.time)
                            })
                        })
                        this.table.technicalCompare.content = content
                        this.viewState = 3
                    }
                    break;
            }

        },
        findRelated(){//找相关
            this.viewState = 0
            this.$refs.tree.handleCheck({checked:false,nodeKey:this.tree[0].nodeKey})
            let findTree = (node,obj)=>{
                if(node.name == obj.name){
                    node.value = obj.value
                    this.$refs.tree.handleCheck({checked:true,nodeKey:node.nodeKey})
                }else{
                    node.children.forEach((child)=>{
                        findTree(child,obj)
                    })
                }
            }
            let obj = this.objDeepCopy(this.table.compare.currentRow)
            obj.value = obj[this.table.compare.currentColumn.slot]
            findTree(this.tree[0],obj)

        },
        listSelectChange(e,row,column){//更换构型 技术s 四随 的当前项
            column[row.tableName+"-index"] = e
            let sameProcess = (children,child)=>{
                let isSame = ! this.table.compare.expandColumns.some( (value, index) =>{
                    return child[value.slot] != child[this.table.compare.expandColumns[0].slot];
                })
                child.isNotSame = !isSame
                if((this.compareChecks.sameHideState&&!isSame)||!this.compareChecks.sameHideState){
                    child._index = children.length
                    children.push(child)
                }
            }
            let findContentProcess = (obj,oldObj)=>{
                let children = []
                oldObj.children.forEach((child)=>{
                    child._rowKey = child.name
                    if(child.children.length){
                        findContentProcess(child,child)
                        if(child.children.length){
                            child._index = children.length
                            children.push(child)
                        }
                    }else{
                        sameProcess(children,child)
                    }
                })
                obj.children = children
            }
            let findContent = (condition,obj)=>{
                if(obj[condition.title]){
                    obj = obj[condition.title]
                    if(!condition.children.length){
                        condition[column.slot] = obj
                    }
                }else if(this.isJSON(obj)){
                    obj = JSON.parse(obj)
                }
                condition.children.forEach((child)=>{
                    findContent(child,obj)
                })
            }
            let findOldContent = (condition,oldCondition,list)=>{
                if(condition._rowKey == oldCondition.name){
                    condition.children = []
                    this.$nextTick(()=>{
//                        oldCondition[column.slot+'-index'] = condition[column.slot+'-index']
                        findContent(oldCondition,column[oldCondition.tableName+'-list'][column[oldCondition.tableName+'-index']])
                        findContentProcess(condition,oldCondition)
                        if(!condition.children.length){
                            list.splice(condition._index,1)
                        }
                    })
                }else{
                    oldCondition.children.forEach((child)=>{
                        list.forEach((item)=>{
                            if(item._rowKey == oldCondition.name){
                                findOldContent(condition,child,item.children)
                            }
                        })

                    })
                }
            }

            this.table.compare.content.forEach((item)=>{
                findOldContent(row,item,this.table.compare.viewList)
            })
        },
        tableClick(row, column, data, event){
            if(row[column.slot]){
                this.table.compare.currentRow = row
                this.table.compare.currentColumn = column
            }
        },
        tableCompareContent(){
            let oldContent = this.objDeepCopy(this.table.compare.content)
            let content = []
            let sameProcess = (children,child)=>{
                let isSame = ! this.table.compare.expandColumns.some( (value, index) =>{
                    return child[value.slot] != child[this.table.compare.expandColumns[0].slot];
                })
                child.isNotSame = !isSame
                if((this.compareChecks.sameHideState&&!isSame)||!this.compareChecks.sameHideState){
                    children.push(child)
                }
            }
            let findContentProcess = (obj)=>{
                let children = []
                obj.oldChildren = this.objDeepCopy(obj.children)
                obj.children.forEach((child)=>{
                    if(child.children.length){
                        findContentProcess(child)
                        if(child.children.length){
                            children.push(child)
                        }
                    }else{
                        sameProcess(children,child)
                    }
                })
                obj.children = children
                if(obj.children){
                    obj._showChildren = true
                }
            }
            oldContent.forEach((item)=>{
                if(this.compareChecks.radioTitle == item.title ){//this.compareChecks.checkList.indexOf(item.title)>=0
                    if(item.children.length){
                        item._showChildren = true
                        findContentProcess(item)
                        if(item.children.length){
                            content.push(item)
                        }
                    }else{
                        sameProcess(content,item)
                    }
                }
            })
            this.table.compare.viewList = content.filter((item,index)=>{
                return true
            })
        },
        isJSON (str){
            if (typeof str == 'string') {
                try {
                    var obj=JSON.parse(str);
                    if(typeof obj == 'object' && obj ){
                        return true;
                    }else{
                        return false;
                    }

                } catch(e) {
                    return false;
                }
            }
        },
        onCompare(){//进入对比分析页
            this.table.compare.currentColumn = null
            this.table.compare.currentRow = null
            this.viewState = 2
            this.table.compare.expandColumns = []
            this.table.airList.chooseList.forEach((item,index)=>{
                this.table.compare.expandColumns.push({
                    slot:'air-'+index,
                    title:item.name,
                    batchId:item.batchId,
                    stateId:item.stateId,
                    baselineId:item.baselineId,
                    minWidth:200,
                    id:item.id,
                    align:'center'
                })
            })
            this.table.batchList.chooseList.forEach((item,index)=>{
                this.table.compare.expandColumns.push({
                    slot:'batch-'+index,
                    title:item.name,
                    batchId:item.batchId,
                    stateId:item.stateId,
                    baselineId:item.baselineId,
                    minWidth:200,
                    id:item.id,
                    align:'center'
                })
            })
            this.table.compare.columns = [{
                key:"title",
                title:"项目",
                tree:true,
                fixed: "left",
                width:400,
                resizable:true
            }].concat(this.table.compare.expandColumns)
            let findContent = (column,condition,obj)=>{
                if(obj){
                    if(this.isJSON(obj)){
                        obj = JSON.parse(obj)
                    }
                    if(obj[condition.title]){
                        obj = obj[condition.title]
                        if(!condition.children.length){
                            condition[column.slot] = obj
                        }
                    }
                    condition.children.forEach((child)=>{
                        findContent(column,child,obj)
                    })
                }else{
                    if(condition.tableName != "批次"&&condition.tableName != "状态"){
                        if(condition.tableName&&DB.tableNos[condition.tableName]&&column[condition.tableName+'-list']===undefined){
                            condition[column.slot+'-header'] = true
                            let contentList = DB.Select(condition.tableName,'"基线id"='+column.baselineId,"")
                            switch (condition.tableName){
                                case "随机设备工具":
                                case "机械专业随机备件目录":
                                case "随机资料目录":
                                case "随机资料配套目录":
                                    contentList = contentList.filter((item)=>{
                                        return item['序号'].indexOf(condition.no) >= 0
                                    })
                                case "技术状态文件":
                                case "构型":
                                case "有寿件":
                                case "双流水":
                                case "交付遗留问题及处置情况":
                                    column[condition.tableName+'-list'] = contentList
                                    column[condition.tableName+'-index'] = 0
                                    break;
                            }
                            if(column[condition.tableName+'-list']&&column[condition.tableName+'-list'].length){
                                obj = column[condition.tableName+'-list'][0]
                            }
                        }
                        condition.children.forEach((child)=>{
                            findContent(column,child,obj)
                        })
                    }
                }
            }
            this.table.compare.expandColumns.forEach((expandColumn)=>{
                this.table.compare.content.forEach((item)=>{
                    if(item.tableName == "批次"||item.tableName == "状态"){
                        let objId = 0
                        if(item.tableName == "批次"){
                            objId = expandColumn.batchId
                        }else{
                            objId = expandColumn.stateId
                        }
                        if(expandColumn[item.tableName+'-obj']===undefined){
                            expandColumn[item.tableName+'-obj'] = DB.selectOne(item.tableName,'id='+objId)
                        }
                    }
                    findContent(expandColumn,item,expandColumn[item.tableName+'-obj'])
                })
            })
//            this.compareChecks.checkAllState = true
//            let list = []
//            this.compareChecks.contentList.forEach((item)=>{
//                list.push(item.title)
//            })
//            this.compareChecks.checkList = list
            this.compareChecks.radioTitle == this.compareChecks.contentList[0].title
            this.tableCompareContent()

        },
        airTableSelect(selection,row){//筛选结果页单机选择
              this.table.airList.chooseList = selection
        },
        batchTableSelect(selection,row){//筛选结果页批次选择
            this.table.batchList.chooseList = selection
        },
        handleCheckAll () {
            if (this.compareChecks.indeterminate) {
                this.compareChecks.checkAllState = false;
            } else {
                this.compareChecks.checkAllState = !this.compareChecks.checkAllState;
            }
            this.compareChecks.indeterminate = false;

            if (this.compareChecks.checkAllState) {
                let list = []
                this.compareChecks.contentList.forEach((item)=>{
                    list.push(item.title)
                })
                this.compareChecks.checkList = list
            } else {
                this.compareChecks.checkList = []
            }
        },//对比页条件全选
        checkAllGroupChange (data) {//对比页条件选择
            if (data.length === this.compareChecks.contentList.length) {
                this.compareChecks.indeterminate = false;
                this.compareChecks.checkAllState = true;
            } else if (data.length > 0) {
                this.compareChecks.indeterminate = true;
                this.compareChecks.checkAllState = false;
            } else {
                this.compareChecks.indeterminate = false;
                this.compareChecks.checkAllState = false;
            }
        },
        selectChange(e,row){//检索条件页下拉表单
            this.table.condition.content[row._index].compareValue = row.compareValue
            this.table.condition.content[row._index].content = row.content
        },
        fileChange(e,row){//检索条件页文件表单
            let file = e.target.files[0]
            if(file){
                let file_reader = new FileReader();
                let blob = null
                file_reader.onload = () => {
                    let fc = file_reader.result;
                    blob = new Blob([fc])
                    this.table.condition.content[row._index].content = blob
                };
                file_reader.readAsArrayBuffer(file);
//                this.technologyStateFileInfoList[row._index].name = file.name
            }
        },
        inputChange(e,row){//检索条件页输入表单
            this.table.condition.content[row._index].content = row.content
        },
        search(){
            let batchList = []
            let airList = []
            this.batchList.forEach((item)=>{
                batchList.push({
                    id:item.id,
                    baselineId:item.baselineId,
                    name:item.title,
                    batchId:item.batchId,
                    stateId:item.stateId
                })
            })
            this.airList.forEach((item)=>{
                airList.push({
                    id:item.id,
                    baselineId:item.baselineId,
                    name:item.title,
                    batchId:item.batchId,
                    stateId:item.stateId
                })
            })
            let compare = (item)=>{
                let str = ""
                switch (item.compareValue){
                    case "=":
                    case ">=":
                    case "<=":
                    case ">":
                    case "<":
                        str = " "+item.compareValue+" "+item.content
                        break;
                    case "包含":
                        str = " LIKE %"+item.content+"%"
                        break
                }
                return str
            }
            let filter = (aList,bList)=>{
                return aList.filter((a)=>{
                    let c = bList.find((b)=>{
                        let flag = false
                        if(a.baselineId == b.baselineId){
                            flag = true
                            a.name = b.name
                            a.batchId = b.batchId
                            a.stateId = b.stateId
                        }
                        return flag
                    })
                    return  c != null
                })
            }
            let unique = (arr)=>{
                for(var i = 0;i<arr.length;i++){
                    for(var j = i+1; j<arr.length;j++){
                        if(arr[i].id == arr[j].id){
                            arr.splice(j,1)
                            j--
                        }
                    }
                }
                return arr
            }
            //递归找子字段并比较
            let findChild = (obj,index,keyList,condition)=>{//obj：此时的字段  index：keylist的下标 keylist:层级数组  condition:条件对象
                let flag = false
                let child = obj[keyList[index]]
                if(child){
                    if(keyList[index+1]){
                        if(this.isJSON(child)){
                            child = JSON.parse(child)
                        }
                        flag = findChild(child,index+1,keyList,condition)
                    }else{
                        switch (condition.compareValue){
                            case "=":
                                if(condition.content == child){
                                     flag = true
                                }
                                break;
                            case ">":
                                if(condition.content > child){
                                    flag = true
                                }
                                break;
                            case ">=":
                                if(condition.content >= child){
                                    flag = true
                                }
                                break;
                            case "<":
                                if(condition.content < child){
                                    flag = true
                                }
                                break;
                            case "<=":
                                if(condition.content <= child){
                                    flag = true
                                }
                                break;
                            case "包含":
                                if(child.indexOf(condition.content) >= 0){
                                    flag = true
                                }
                                break;
                        }
                    }
                }
                return flag
            }
            this.table.condition.content.forEach((item)=>{
                if(item.content){
                    let filterList = []
                    let keyList = item.name.split(".")
                    if(keyList[0] == item.tableName) {//构型 技术状态文件
                        keyList.splice(0,1)
                    }
                    let obj
                    switch (item.tableName){
                        case '批次':
                            airList.forEach((air)=>{
                                obj = DB.selectOne(item.tableName,"id = "+air.batchId,"")
                                for(var key in obj){
                                    if(air[key] === undefined){
                                        air[key] = obj[key]
                                    }
                                }
                            })
                            batchList.forEach((batch)=>{
                                obj = DB.selectOne(item.tableName,"id = "+batch.batchId,"")
                                for(var key in obj){
                                    if(batch[key] === undefined){
                                        batch[key] = obj[key]
                                    }
                                }
                            })
                            break;
                        case '状态':
                            airList.forEach((air)=>{
                                obj = DB.selectOne(item.tableName,"id = "+air.stateId,"")
                                for(var key in obj){
                                    if(air[key] === undefined){
                                        air[key] = obj[key]
                                    }
                                }
                            })
                            batchList.forEach((batch)=>{
                                obj = DB.selectOne(item.tableName,"id = "+batch.stateId,"")
                                for(var key in obj){
                                    if(batch[key] === undefined){
                                        batch[key] = obj[key]
                                    }
                                }
                            })
                            break;
                        default:
                            filterList = DB.Select(item.tableName,"","")
                            filterList.forEach((f)=>{
                                f.baselineId = f['基线id']
                            })
                            //数据代替了当前字段 需要把批次 单机的name赋值过来
                            airList = filter(filterList,airList)
                            batchList = filter(filterList,batchList)
                            if(keyList[0] == '保障资源'){
                                let keyName = item.name.replace("保障资源."+item.configName+".","")
                                keyList = keyName.split(".")
                            }
                            break;
                    }
                    airList = airList.filter((f)=>{
                        return findChild(f,0,keyList,item)
                    })
                    batchList = batchList.filter((f)=>{
                        return findChild(f,0,keyList,item)
                    })
                }
            })
            this.table.airList.content = unique(airList)
            this.table.batchList.content = unique(batchList)
            this.viewState = 1
        },//检索条件页 检索事件
        refresh(){//重置
            this.viewState = 0
            this.$refs.tree.handleCheck({checked:false,nodeKey:this.tree[0].nodeKey})
        },
        remove(row){//检索条件页  删除一个条件
            this.selectTree(this.tree[0],row.nodeKey)
        },
        selectTree(node,nodeKey){//删除条件的递归事件
            if(node.nodeKey == nodeKey){
                this.$set(node,'checked',false)
                this.$refs.tree.handleCheck({checked:node.checked,nodeKey:node.nodeKey})
            }else{
                node.children.forEach((child)=>{
                    this.selectTree(child,nodeKey)
                })
            }
        },
        nodeCheck(nodeList,node){//检索条件页 右边的树选中事件
            let contentList = this.objDeepCopy(this.table.condition.content)
            contentList = contentList.filter((item)=>{
                let findItem = nodeList.find((newItem)=>{
                    return item.nodeKey == newItem.nodeKey
                })
                return  findItem != null
            })
            nodeList.forEach((item)=>{
                let newItem = this.objDeepCopy(item)
                newItem.content = item.value
                item.value = ""
                delete newItem['children']
                if(newItem.type != 2 && newItem.type !=3){
                    let newContentList = this.objDeepCopy(contentList).reverse()
                    let indexA = newContentList.findIndex((oldItem)=>{
                        return oldItem.nodeKey < item.nodeKey
                    })
                    let indexB = newContentList.findIndex((oldItem)=>{
                        return oldItem.nodeKey == item.nodeKey
                    })
                    if(indexB<0){
                        if(indexA>=0){
                            indexA = contentList.findIndex((findItem)=>{
                                return findItem.nodeKey == newContentList[indexA].nodeKey
                            })
                            contentList.splice(indexA+1,0,newItem)
                        }else if(indexA<0){
                            contentList.unshift(newItem)
                        }
                    }
                }
            })
            this.table.condition.content = contentList
        },
        addParentName(childName,parentName){
            return parentName+"."+childName
        },
        createTree(){// 创建右边的条件树
            let batchKeyList = DB.getSchemaComponents(this.templateId,DB.Select("lists","parent_no="+DB.tableNos['批次'],"")[0].element_type_no)
            let stateKeyList = DB.getSchemaComponents(this.templateId,DB.Select("lists","parent_no="+DB.tableNos['状态'],"")[0].element_type_no)
            let tree = {
                title:"全部",
                expand:true,
                type:2,
                children:[
                    {title:"使命任务",},
                    {title:"布局"},
                    {title:"主要技术参数"},
                    {title:"主要配备"},
                    {title:"构型"},
                    {title:"保障资源"},
                    {title:"喷涂方案"},
                    {title:"技术状态文件"},
                    {title:"有寿件"},
                    {title:"双流水"},
                    {title:"交付遗留问题及处置情况"}]
            }
            tree.children.forEach((item,index)=>{
                item.name = item.title
                item.children = []
                switch (item.title){
                    case "使命任务":
                        stateKeyList.forEach((key)=>{
                            if(key.name == "使命任务"){
                                item = this.createNode(item,key)
                                item.tableName = "状态"
                                this.createType(item)
                            }
                        })
                        break;
                    case "布局":
                    case "主要技术参数":
                    case "主要配备":
                    case "喷涂方案":
                        batchKeyList.forEach((key)=>{
                            if(key.name == item.title){
                                item = this.createNode(item,key)
                                item.tableName = "批次"
                                this.createType(item)
                            }
                        })
                        break;
                    case "构型":
                    case "技术状态文件":
                    case "有寿件":
                    case "双流水":
                    case "交付遗留问题及处置情况":
                        item.type = 3
                        item.tableName = item.title
                        let lists = DB.Select("lists","parent_no="+DB.tableNos[item.title],"")
                        if(lists[0]){
                            let listKeyList = DB.getSchemaComponents(this.templateId,lists[0].element_type_no)
                            listKeyList.forEach((key)=>{
                                if(key.name.indexOf('id')<0){
                                    let child = this.createNode({},key,item)
                                    this.createType(child)
                                    if(child.type!=3&&((child.children.length&&child.type == 2)||child.type!=2)){
                                        item.children.push(child)
                                    }
                                }
                            })
                        }
                        break;
                    case "保障资源":
                        item.type = 2
                        item.children = DB.Select("support_configs","parent_id is null","")
                        item.children.forEach((config,configIndex)=>{
                            config.type = 2
                            config.title = config.name
                            config.name = this.addParentName(config.name,item.name)
                            config.children = DB.Select("support_configs","parent_id ="+config.id+" AND template_id="+this.templateId,"")
                            config.children.forEach((child,childIndex)=>{
                                let lists = DB.getTableSchema(this.templateId,child.table_no)
                                child.type = 3
                                child.no = Number(index+1) +"."+ Number(configIndex+1) +"."+Number(childIndex+1)
                                child.children = []
                                child.title = child.name
                                child.name = this.addParentName(child.title,config.name)
                                let listsType = DB.getTypeByTemplateIdAndNo(this.templateId,lists.parent_no)
                                child.tableName = listsType.name
                                child.configName = config.title
                                lists.element_type.children.forEach((key)=>{
                                    if(key.name.indexOf('id')<0){
                                        let node = this.createNode({},key,child)
                                        this.createType(node)
                                        child.children.push(node)
                                    }
                                })
                            })
                        })
                        break;
                }
            })
            this.tree = [tree]
            this.table.compare.content = this.objDeepCopy(tree.children)
        },
        createNode(item,object,parent){
            item.title = object.name
            item.typeNo = object.type_no
            item.schemaId = object.id
            if(parent){
                item.configName = parent.configName
                item.tableName = parent.tableName
                item.name = this.addParentName(object.name,parent.name)
            }else{
                item.name = object.name
            }
            return item
        },
        createType(node){
            let type = this.types.find((item)=>{
                return item.no == node.typeNo
            })
            if(type){
                node.children = []
                node.type = type.type
                node.typeName = type.name
                switch (type.type){
                    case 0:
                        switch (node.typeNo){
                            case 1:
                            case 2:
                                node.compareType = "0"
                                node.inputType = "text"
                                break;
                            case 3:
                            case 4:
                            case 5:
                                node.compareType = "1"
                                node.inputType = "number"
                                break;
                            case 6:
                            case 7:
                                node.compareType = "1"
                                node.inputType = "date"
                                break;
                            case 8:
                            case 9:
                                node.compareType = "2"
                                node.inputType = "url"
//                                node.type = 2
                                break;
                        }
                        node.compareValue = "="
                        break;
                    case 1:
                        node.compareType = "2"
                        node.selects = DB.getCodeValues(this.templateId,node.typeNo)
                        node.compareValue = "="
                        break;
                    case 2:
                        let list = this.schemas.filter((item)=>{
                            return item.parent_no == type.no && (item.template_id == this.templateId  || item.template_id == null)
                        })
                        list.forEach((item)=>{
                            let child = this.createNode({},item,node)
                            this.createType(child)
                            if(child.type!=3&&((child.children.length&&child.type == 2)||child.type!=2)){
                                node.children.push(child)
                            }
                        })
                        break;
                    case 3:

                        break;
                }
            }else{
                console.log(node)
                console.log("类型查询失败")
            }

        },
        objDeepCopy (source) {
            var _typeof = typeof Symbol === "function" && typeof Symbol.iterator === "symbol" ? function (obj) { return typeof obj; } : function (obj) { return obj && typeof Symbol === "function" && obj.constructor === Symbol && obj !== Symbol.prototype ? "symbol" : typeof obj; };
            var sourceCopy = source instanceof Array ? [] : {};
            for (var item in source) {
                sourceCopy[item] = _typeof(source[item]) === 'object' ? this.objDeepCopy(source[item]) : source[item];
            }
            return sourceCopy;
        }
    }
}
</script>

