        modalCancel(){
            switch (this.modal.viewState){
                case 'model':
                    if(this.modal.model.isOutSide){
                        this.modal.model.isOutSide = false
                        this.modal.viewState = 'air'
                        this.updateAirModal()
                    }else{
                        this.modal.showState = false
                    }
                    break;
                case 'organization':
                    if(this.modal.organization.isOutSide){
                        this.modal.organization.isOutSide = false
                        this.modal.viewState = 'air'
                        this.updateAirModal()
                    }else{
                        this.modal.showState = false
                    }
                    break;
                default:
                    this.modal.showState = false
                    break;
            }
        },
        modalConfirm(){
            switch (this.modal.viewState){
                case 'template':
                    this.appendTagClick("新建模板")
                    this.modal.showState = false
                    break;
                case 'templateModel':
                    this.intoChooseTemplateModel()
                    break;
                case 'model':
                    this.createModel()
                    break;
                case 'air':
                    this.createAir()
                    break;
                case 'organization':
                    this.createOrganization()
                    break;
            }
        },
