        intoCreateModel(){
            if(this.modal.model.templateIndex >= 0){
                this.modal.model.codeList = []
                let templateId = this.templateList[this.modal.model.templateIndex].id
                this.typeList.forEach((type)=>{
                    type.id = Number(type.id)
                    if(type.name == "飞机类型" && type.template_id == templateId){
                        this.schemaList.forEach((schema)=>{
                            if(schema.name == "类型"&&schema.type_no == type.no){
                                this.codeList.forEach((code)=>{
                                    if(code.parent_no == schema.type_no && code.template_id == templateId){
                                        this.modal.model.codeList.push(DB.extendDeep({},code))
                                    }
                                })
                            }
                        })
                    }
                })
                this.modal.viewState = 'model'
                this.modal.title = this.templateList[this.modal.model.templateIndex].name+"-机型信息"
            }
        },
        checkTemplate(index){
            this.modal.template.index = index
        },
        checkTemplateModel(index){
            if(this.modal.model.templateIndex == index){
                this.modal.model.templateIndex = -1
            }else{
                this.modal.model.templateIndex = index
            }
        },
        createFillChildren(object){
            let children = [                {title:"使命任务"},
                {title:"布局"},
                {title:"主要技术参数"},
                {title:"主要配备"},
                {title:"构型"},
                {title:"保障资源"},
                {title:"喷涂方案"},
                {title:"技术状态文件"},
                {title:"有寿件"},
                {title:"双流水"},
                {title:"交付遗留问题及处置情况"}]
            children.forEach((item,index)=>{
                item.isCanEdit = true
                item.children = []
                item.stateId = object.stateId
                item.batchId = object.batchId
                item.modelId = object.modelId
                item.templateId = object.templateId
                item.baselineId = object.baselineId
                item.configurationId = object.configurationId
                item.dataType = "fill-property"
                item.iconType = "word"
                if(item.title == '保障资源'){
                    item.iconType = 'tree'
                    item.dataType = 'expand'
                    item.children = DB.Select("support_configs","parent_id is null","")
                    item.children.forEach((support,supportIndex)=>{
                        support.dataType = 'expand'
                        support.iconType = 'tree'
                        support.title = support.name
                        support.children = DB.Select("support_configs","parent_id ="+support.id+" AND template_id="+item.templateId,"")
                        support.children.forEach((child,childIndex)=>{
                            child.title = child.name
                            child.dataType = "fill-property"
                            child.nodeType = "support_configs"
                            child.iconType = "word"
                            child.isCanExport = true
                            child.isCanPaste = true
                            child.isCanEdit = true
                            child.stateId = item.stateId
                            child.idRule = Number(index+1) +"."+ Number(supportIndex+1) +"."+Number(childIndex+1)
                            child.batchId = item.batchId
                            child.modelId = item.modelId
                            child.templateId = item.templateId
                            child.baselineId = item.baselineId
                            child.configurationId = item.configurationId
                        })
                    })
                }
            })
            return children
        },
