                            batch.id = Number(batch.id)
                            batch.dataType = "batch"
                            if(batch["状态id"] == state.id){
                                let currentBaselineId = (this.baselineList.find((baseline)=>{
                                    return baseline["目标id"] == batch.id && baseline["适用目标"] == 0
                                })).id
                                let newBatch = {
                                    title: batch["编号"],
                                    pageIndex:0,
                                    id:batch.id,
                                    stateId:state.id,
                                    batchId:batch.id,
                                    modelId:model.id,
                                    templateId:model["模板id"],
                                    baselineId:currentBaselineId,
                                    configurationId:(this.configurationList.find((configuration)=>{
                                        return configuration["基线id"] == currentBaselineId&&configuration.parent_id === null
                                    })).id,
                                    isCanEdit:true,
                                    iconType:"word",
                                    isCanCopy:true,
                                    children:[],
                                    dataType:batch.dataType,
                                }
                                newBatch.children = this.createFillChildren(newBatch)
                                if(newBatch.baselineId == this.$route.query.id){
                                    newBatch.selected = true
                                    this.chooseData = newBatch
                                }
                                batchList.push(
                                    newBatch
                                )
                            }
                        })
                        stateList.push(
                            {
                                title: state["编号"],
                                pageIndex:0,
                                id:state.id,
                                templateId:model["模板id"],
                                modelId:model.id,
                                isCanEdit:true,
                                iconType:"tree",
                                children:batchList,
                                dataType:state.dataType,
                                expand:this.chooseData!=null&&this.$route.query.dataType == 'batch'
                            }
                        )
                    }
                })
                this.pageList[0].treeList.push(
                    {
                        title: model["名称"],
                        pageIndex:0,
                        id:model.id,
                        templateId:model["模板id"],
                        dataType:model.dataType,
                        iconType:"tree",
                        isCanEdit:true,
                        children:stateList,
                        expand:this.chooseData!=null&&this.$route.query.dataType == 'batch'
                    }
                )
            })
            this.organizationList.forEach((organization)=>{
                organization.dataType = "organization"
                if(organization['类型'] == 2){
                    let airList = []
                    this.airList.forEach((air)=>{
                        air.dataType = "air"
                        if(organization.id == air["接装单位id"]){
                            let batch = DB.selectOne('批次', 'id='+ air["批次id"])
                            let state = DB.selectOne('状态', 'id='+ batch["状态id"])
                            let model = DB.selectOne('机型', 'id='+ state["机型id"])
                            let baseline = DB.selectOne('基线', '"目标id"='+ air.id+' AND "适用目标"=1')
                            let configuration = DB.selectOne('构型', '"基线id"='+ baseline.id)
                            let newAir = {
                                title: air["编号"],
                                no: air["编号"],
                                pageIndex:1,
                                id:air.id,
                                organizationId:organization.id,
                                batchId:batch.id,
                                stateId:state.id,
                                modelId:model.id,
                                templateId:model["模板id"],
                                configurationId:configuration.id,
                                baselineId:baseline.id,
                                isCanEdit:true,
                                isCanCopy:true,
                                iconType:"word",
                                children:[],
                                dataType:air.dataType
                            }
                            newAir.children = this.createFillChildren(newAir)
                            if(newAir.baselineId == this.$route.query.id){
                                newAir.selected = true
                                this.chooseData = newAir
                            }
                            airList.push(newAir)
                        }
                    })
                    this.pageList[1].treeList.push(
                        {
                            title: organization['名称'],
                            pageIndex:1,
                            id:organization.id,
                            dataType:organization.dataType,
                            templateId:1,
                            iconType:"tree",
                            isCanEdit:true,
                            children:airList,
                            expand:this.chooseData!=null&&this.$route.query.dataType == 'air'
                        }
                    )
                }
            })
            this.$nextTick(()=>{
                this.setChooseData(this.chooseData)
            })
        },
