        },
        createTemplateChildren(template){
            let batchKeyList = DB.getSchemaComponents(template.id,DB.Select("lists","parent_no="+DB.tableNos['批次'],"")[0].element_type_no)
            let stateKeyList = DB.getSchemaComponents(template.id,DB.Select("lists","parent_no="+DB.tableNos['状态'],"")[0].element_type_no)
            let children = [                {title:"使命任务",},
                {title:"布局"},
                {title:"主要技术参数"},
                {title:"主要配备"},
                {title:"构型"},
                {title:"保障资源"},
                {title:"喷涂方案"},
                {title:"技术状态文件"},
                {title:"有寿件"},
                {title:"双流水"},
                {title:"交付遗留问题及处置情况"}]
            let batchExtend = DB.getSchemaComponents(template.id,265)
            children = children.concat(batchExtend)
            children.forEach((item)=>{
                item.dataType = "type-property"
                item.iconType = "word"
                item.isCanEdit = true
                item.templateId = template.id
                if(item.id){
                    item.isCanRemove = true
                    item.title = item.name
                    item.schema = batchExtend.find((key)=>{
                        return key.id == item.id
                    })
                    item.type = DB.getTypeByTemplateIdAndNo(template.id,item.schema.type_no)
                    item.nodeType = 'batchExtend'
                }else{
                    switch (item.title){
                        case "使命任务":
                            stateKeyList.forEach((key)=>{
                                if(key.name == "使命任务"){
                                    item.schema = key
                                    item.type = DB.getTypeByTemplateIdAndNo(template.id,key.type_no)
                                }
                            })
                            break;
                        case "布局":
                        case "主要技术参数":
                        case "主要配备":
                        case "喷涂方案":
                            batchKeyList.forEach((key)=>{
                                if(key.name == item.title){
                                    item.schema = key
                                    item.type = DB.getTypeByTemplateIdAndNo(template.id,key.type_no)
                                }
                            })
                            break;
                        case "构型":
                        case "技术状态文件":
                        case "有寿件":
                        case "双流水":
                        case "交付遗留问题及处置情况":
                            item.type = DB.getTypeByTemplateIdAndNo(template.id,DB.tableNos[item.title])
                            break;
                        case "保障资源":
                            item.iconType = 'tree'
                            item.dataType = 'expand'
                            item.children = DB.Select("support_configs","parent_id is null","")
                            item.children.forEach((support)=>{
                                support.dataType = 'expand'
                                support.iconType = 'tree'
                                support.title = support.name
                                support.children = DB.Select("support_configs","parent_id ="+support.id+" AND template_id="+item.templateId,"")
                                support.children.forEach((child)=>{
                                    child.title = child.name
                                    child.dataType = "type-property"
                                    child.iconType = "word"
                                    child.isCanEdit = true
                                    child.isCanRemove = true
                                    child.templateId = template.id
                                    child.nodeType = 'supportConfig'
                                    child.type = DB.getTypeByTemplateIdAndNo(template.id,child.table_no)
                                })
                                support.children.push({
                                    title:'新建随机数据表',
                                    iconType:'append',
                                    templateId:template.id,
                                    parentId:support.id
                                })
                            })
                            break;
                    }
                }

            })
            children.push({
                title:'新建扩展',
                iconType:'append',
                templateId:template.id,
            })
            return children
        },
        onShowBasisData(){
            this.pageList = [
                {
                    name:"基础数据",
                    treeList:[{
                        title:'基础数据',
                        iconType:'tree',
                        dataType:'basisData',
                        expand:true,
                        isCanSend:true,
                        children:[
                            {
                                title:'接装单位',
                                dataType:'basisData',
                                isCanSend:true,
                            },
                            {
                                title:'承制单位',
                                dataType:'basisData',
                                isCanSend:true,
                            }
                        ]
                    }],
                    appendTagList:[],
                    appendTagShowState:false,
                }]
        },
        updateTypeList(){
            this.typeList = DB.Select("types","","")
            this.typeList.forEach((item)=>{
                item.title = item.name
                item.dataType = "type"
                switch (item.type) {
                    case 0:
                        switch (item.id) {
                            case 1:
                                item.iconType='text'
                                break;
                            case 2:
                                item.iconType='richText'
                                break;
                            case 3:
                                item.iconType='numeric'
                                break;
                            case 4:
                                item.iconType='integer'
                                break;
                            case 5:
                                item.iconType='real'
                                break;
                            case 6:
                                item.iconType='date'
                                break;
                            case 7:
                                item.iconType='time'
                                break;
                            case 8:
                                item.iconType='data'
                                break;
                            case 9:
                                item.iconType='image'
                                break;
                        }
                        break;
                    case 1:
                        item.iconType='select'
                        break;
                    case 2:
                        item.iconType='map'
                        break;
                    case 3:
                        item.iconType='array'
                        break;
                }
            })
        },
        removeItem(){
            if(this.chooseData){
                if(this.chooseData.dataType == "fill-property" && this.chooseData.title == '构型'){
                    Bus.$emit('configuration-edit-delete')
                }
                if(this.chooseData.node&&this.chooseData.isCanDelete){
                    let chooseData =null
                    switch (this.chooseData.dataType){
                        case "template":
                            let model = DB.selectOne('机型','"模板id"='+this.chooseData.id,)
                            if(model){
                                this.$Message.error('已有机型选用此模板，删除失败！')
                                return
                            }
                            DB.Update('templates',{
                                id:this.chooseData.id,
                                state:1
                            })
                            Bus.$emit('design-choose',null)
                            break;
                        case "batch":
                            DB.Update('批次',{
                                id:this.chooseData.id,
                                state:1
                            })
                            this.pageList[1].treeList.forEach((tree)=>{
                                tree.children = tree.children.filter(item => item.batchId != this.chooseData.id);
                            })
                            let airList = DB.Select("飞机",`"批次id" = ${this.chooseData.id}`,"")//"批次id="+this.chooseData.id
                            airList.forEach((item)=>{
                                DB.Update('飞机',{
                                    id:item.id,
                                    state:1
                                })
                            })
                            break;
                        case "air":
                            DB.Update('飞机',{
                                id:this.chooseData.id,
                                state:1
                            })
                            break;
                    }
                    this.remove(this.chooseData.root,this.chooseData.node,this.chooseData)
                    this.setChooseData(chooseData)

                }
            }
        },
        appendTagSwitch(page){
            if(page.appendTagList.length){
                page.appendTagShowState = !page.appendTagShowState
            }else{
                if(this.chooseData){
                    this.append(this.chooseData)
                }else{
                    switch (this.routePath){
                        case "/fill":
                            if(this.pageIndex == 0){
                                this.intoChooseTemplateModel()
                            }else if(this.pageIndex == 1){
                                this.intoCreateOrganization()
                            }
                            break;
                    }
                }
            }
        },
        choosePage(index){
            if(this.pageIndex!=index&&this.chooseData){
                this.$set(this.chooseData,'selected',false)
                this.setChooseData(null)
            }
            this.pageIndex = index
        },
        appendTagClick(name){
            switch (name) {
                case '枚举':
                    if(this.chooseData&&this.chooseData.isCanAppend){
                        this.append(this.chooseData,{name:"新建"+name+"类型",iconType:'select',type:1})
                    }
                    break;
                case '复合':
                    if(this.chooseData&&this.chooseData.isCanAppend){
                        this.append(this.chooseData,{name:"新建"+name+"类型",iconType:'map',type:2})
                    }
                    break;
                case '表格':
                    if(this.chooseData&&this.chooseData.isCanAppend){
                        this.append(this.chooseData,{name:"新建"+name+"类型",iconType:'array',type:3})
                    }
                    break;
                case '新建模板':
                    let newTemplate = {
                        type:-2,
                        title: "新建模板",
                        dataType:"template",
                        isCanExport:true,
                        pageIndex:1,
                        selected:true,
                        isCanDelete:true,
                        isNew:true,
                        isCanEdit:true,
                        children:[],
                        expand:true,
                        iconType:'tree',
                    }
                    let cloneTemplateId = this.templateList[this.modal.template.index].id
                    if(this.chooseData){
                        this.chooseData.selected = false
                    }
                    this.spinShow = true
                        setTimeout(()=>{
                            DB.cloneTemplate(cloneTemplateId,newTemplate.title,"").then((templateId)=>{
                                newTemplate.id = templateId
                                newTemplate.children = this.createTemplateChildren(newTemplate)
                                this.pageList[0].treeList.splice(this.pageList[0].treeList.length-1,0,newTemplate)
                                this.setChooseData(newTemplate)
                                this.spinShow = false
                            }).catch(()=>{
                                this.spinShow = false
                            })
                        },300)
                    break;
            }
        },
        nodeClick(nodeList,node){
            if(node.iconType=='append'){
                node.selected = false
                const parentKey = node.root.find(el => el.nodeKey === node.nodeKey).parent;
                let index
                let parent
                if(parentKey!=null){
                    parent = node.root.find(el => el.nodeKey === parentKey).node;
                    index = parent.children.indexOf(node);

                }
                let child
                switch (node.title){
                    case '新建模板':
                        this.intoChooseTemplate()
                        if(this.chooseData){
                            this.chooseData.selected = true
                        }
                        break;
                    case '新建随机数据表':
                        if(this.chooseData){
                            this.chooseData.selected = false
                        }
                        child = {
                            "template_id": node.templateId,
                            "parent_id": node.parentId,
                            "name": "未命名随机数据表",
                            "table_no": 74
                        }
                        child.id = DB.Insert('support_configs',child)
                        child.title = child.name
                        child.dataType = "type-property"
                        child.iconType = "word"
                        child.isCanEdit = true
                        child.isCanRemove = true
                        child.templateId = node.templateId
                        child.nodeType = 'supportConfig'
                        child.type = DB.getTypeByTemplateIdAndNo(child.templateId,child.table_no)
                        child.selected = true
                        parent.children.splice(index, 0 ,child);
                        this.$nextTick(()=>{
                            this.setChooseData(child)
                        })
                        break;
                    case '新建扩展':
                        if(this.chooseData){
                            this.chooseData.selected = false
                        }
                        let no = 0
                        let children = DB.getSchemaComponents(node.templateId,265)
                        if(children.length){
                            no = children[children.length-1].no
                        }
                        child = {
                            "template_id": node.templateId,
                            "parent_no": 265,
                            "no": no,
                            "name": "未命名扩展字段",
                            "type_no": 1,
                            "is_required": 0,
                            "restriction": JSON.stringify({
                                unit: {},
                                min: '',
                                max: '',
                                precision: '',
                                format: ''
                            }),
                            "description": null
                        }
                        child.id = DB.Insert('schemas',child)
                        child.title = child.name
                        child.isCanEdit = true
                        child.isCanRemove = true
                        child.templateId = node.templateId
                        child.iconType = "word"
                        child.dataType = "type-property"
                        child.nodeType = 'batchExtend'
                        child.type = DB.getTypeByTemplateIdAndNo(child.templateId,child.type_no)
                        child.schema = DB.selectOne('schemas','id='+child.id)
                        child.schema.restriction = JSON.parse(child.schema.restriction)
                        child.selected = true
                        parent.children.splice(index, 0 ,child);
                        this.$nextTick(()=>{
                            this.setChooseData(child)
                        })
                        break;
                }
                return
            }
            if(node.dataType=='expand'){
                node.selected = false
                this.$set(node,'expand',!node.expand)
                if(this.chooseData){
                    this.chooseData.selected = true
                }
                return
            }
            if( new Date().getTime() - this.touchtime < 500 ){
                if(nodeList[0]){
                    this.setChooseData(nodeList[0])
                }else{
                    if(this.chooseData&&this.chooseData == node){
                        this.setChooseData(null)
                        node.selected = false //双击取消
                    }
                }
            }else{
                if(this.chooseData&&this.chooseData==node){
                    node.selected = true //单击取消
                }else{
                    if(this.chooseData){
                        this.chooseData.selected = true
                    }
                    node.selected = false
                }
                this.touchtime = new Date().getTime();
            }
        },
        renderContent (h, { root, node, data }) {
            data.root = root
            data.node = node
            let children  = [
                h('span', data.title)
            ]
            if(data.iconType){
                children.unshift(h('Icon', {
                    props: {
                        type: data.iconType?data.iconType:'ios-paper-outline'
                    },
                    style: {
                        marginRight: '8px'
                    }
                }))
            }
            if(data.isCanRemove){
                children.push(h('Button', {
                    props: Object.assign({}, {
                        type: 'default',
                        size: 'small',
                    }, {
                        icon:  'ios-remove'
                    }),
                    style: {
                        display: 'inline-block',
                        float: 'right',
                        marginLeft: '5px'
                    },
                    on: {
                        click: () => { this.remove(root, node, data) }
                    }
                }))
            }
            return h('span', {
                style: {
                    display: 'inline-block',
                    width: '100%',
                    color:data.isNew?'#7070ff':'',
                    opacity:data.isCut?'0.5':'1'
                }
            }, [
                h('span',{style: {
                    display:'flex',
                    'justify-content':'center',
                    'align-items':'center'
                }}, children)
            ]);
        },
        setChooseData(data){
            if(data){
                this.$set(data,'selected',true)
//                data.selected = true
            }
            this.chooseData = data
            Bus.$emit("chooseData",this.chooseData)
            if(this.chooseData){
                if(this.chooseData.isCanEdit || this.chooseData.isCanSend){
                    let value = ""
                    let type
                    let schema
                    let lists
                    switch (this.chooseData.dataType){
                        case "basisData":
                            Bus.$emit("basisData-choose",{
                                name:this.chooseData.title
                            })
                            break;
                        case "trash":
                            if(this.routePath=='/fill'){
                                Bus.$emit("fill-choose",{
                                    dataType:this.chooseData.dataType,
                                })
                            }else if(this.routePath=='/design'){
                                Bus.$emit("design-choose",{
                                    type:-3,
                                },'trash')
                            }
                            break;
                        case "type":
                            Bus.$emit("editType",{
                                id:this.chooseData.id,
                                name:this.chooseData.title,
                                type:this.chooseData.type,
                                template_id:this.chooseData.template_id,
                                no:this.chooseData.no,
                                saved:this.chooseData.isNew?false:true
                            })
                            break;
                        case "template":
                            Bus.$emit("design-choose",{
                                id:this.chooseData.id,
                                name:this.chooseData.title,
                                saved:this.chooseData.isNew?false:true
                            },'template')
                            break;
                        case "model":
                            if(this.routePath.indexOf('/technical')>=0) {
                                Bus.$emit("technical-edit",null)
                            }else{
                                value = DB.selectOne('机型', 'id=' + this.chooseData.id)
                                if (!value) {
                                    value = {}
                                }
                                type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId, DB.selectOne("lists", "parent_no=" + DB.tableNos['机型'], "").element_type_no)
                                Bus.$emit("fill-choose", {
                                    id: this.chooseData.id,
                                    name: this.chooseData.title,
                                    relation: {
                                        templateId: this.chooseData.templateId,
                                    },
                                    type: type,
                                    value: value,
                                }, (childList) => {
                                    return DB.processForeignKey(DB.removeUnneedFields(childList, ['基线id', 'id', '模板id', 'state']))
                                })
                            }
                            break;
                        case "state":
                            if(this.routePath.indexOf('/technical')>=0) {
                                Bus.$emit("technical-edit",null)
                            }else{
                                value = DB.selectOne('状态','id='+this.chooseData.id)
                                if(!value){
                                    value = {}
                                }
                                type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,DB.selectOne("lists","parent_no="+DB.tableNos['状态'],"").element_type_no)
                                Bus.$emit("fill-choose",{
                                    id:this.chooseData.id,
                                    name:this.chooseData.title,
                                    relation: {
                                        modelId:this.chooseData.modelId,
                                        templateId:this.chooseData.templateId,
                                    },
                                    type:type,
                                    value:value,
                                },(childList)=>{
                                    return DB.removeUnneedFields(childList,['基线id','id','模板id','工厂id','state','机型id','使命任务'])
                                })
                            }
                            break;
                        case "batch":
                            if(this.routePath.indexOf('/technical')>=0){
                                Bus.$emit("technical-edit",{
                                    id:this.chooseData.id,
                                    name:this.chooseData.title,
                                    relation: {
                                        modelId:this.chooseData.modelId,
                                        stateId:this.chooseData.stateId,
                                        templateId:this.chooseData.templateId,
                                        configurationId:this.chooseData.configurationId,
                                        baselineId:this.chooseData.baselineId
                                    },
                                })
                            }else if(this.routePath=='/fill'||this.routePath=='/'){
                                value = DB.selectOne('批次','id='+this.chooseData.id)
                                if(!value){
                                    value = {}
                                }
                                type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,DB.selectOne("lists","parent_no="+DB.tableNos['批次'],"").element_type_no)
                                Bus.$emit("fill-choose",{
                                    id:this.chooseData.id,
                                    name:this.chooseData.title,
                                    relation: {
                                        modelId:this.chooseData.modelId,
                                        stateId:this.chooseData.stateId,
                                        templateId:this.chooseData.templateId,
                                        configurationId:this.chooseData.configurationId,
                                        baselineId:this.chooseData.baselineId
                                    },
                                    type:type,
                                    value:value,
                                    dataType:this.chooseData.dataType
                                },(childList)=>{
                                    return DB.removeUnneedFields(childList,['基线id','id','模板id','工厂id','state','状态id','布局','主要技术参数','主要配备','喷涂方案'])
                                })
                            }
                            break;
                        case "organization":
                            if(this.routePath.indexOf('/technical')>=0) {
                                Bus.$emit("technical-edit",null)
                            }else{
                                Bus.$emit("fill-choose",null)
                            }
                            break;
                        case "air":
                            if(this.routePath.indexOf('/technical')>=0){
                                Bus.$emit("technical-edit",{
                                    id:this.chooseData.id,
                                    name:this.chooseData.title,
                                    relation:{
                                        templateId:this.chooseData.templateId,
                                        baselineId:this.chooseData.baselineId,
                                        organizationId:this.chooseData.organizationId,
                                        configurationId:this.chooseData.configurationId,
                                        stateId:this.chooseData.stateId,
                                        batchId:this.chooseData.batchId,
                                        modelId:this.chooseData.modelId
                                    },
                                })
                            }else if(this.routePath=='/fill'||this.routePath=='/'){
                                value = DB.selectOne('飞机','id='+this.chooseData.id)
                                if(!value){
                                    value = {}
                                }
                                type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,DB.selectOne("lists","parent_no="+DB.tableNos['飞机'],"").element_type_no)
                                Bus.$emit("fill-choose",{
                                    id:this.chooseData.id,
                                    name:this.chooseData.title,
                                    relation:{
                                        templateId:this.chooseData.templateId,
                                        baselineId:this.chooseData.baselineId,
                                        organizationId:this.chooseData.organizationId,
                                        configurationId:this.chooseData.configurationId,
                                        stateId:this.chooseData.stateId,
                                        batchId:this.chooseData.batchId,
                                        modelId:this.chooseData.modelId
                                    },
                                    type:type,
                                    dataType:this.chooseData.dataType,
                                    value:value,
                                },(childList)=>{
                                    return DB.processForeignKey(DB.removeUnneedFields(childList,['基线id','id','模板id','state','状态id','批次id','接装单位id']))
                                })
                            }
                            break;
                        case "fill-property":
                            let isRealList = false
                            switch (this.chooseData.title){
                                case '使命任务':
                                    value = DB.selectOne('状态','id='+this.chooseData.stateId)['使命任务']
                                    let stateKeyList = DB.getSchemaComponents(this.chooseData.templateId,DB.selectOne("lists","parent_no="+DB.tableNos['状态'],"").element_type_no)
                                    stateKeyList.forEach((key)=>{
                                        if(key.name == "使命任务"){
                                            schema = key
                                             type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,key.type_no)
                                        }
                                    })
                                    break;
                                case "布局":
                                case "主要技术参数":
                                case "主要配备":
                                case "喷涂方案":
                                    value = DB.selectOne('批次','id='+this.chooseData.batchId)[this.chooseData.title]
                                    if(this.chooseData.title == "喷涂方案"){
                                        if(!value){
                                            value = ""
                                        }
                                    }else{
                                        if(value){
                                            value = JSON.parse(value)
                                        }else{
                                            value = {}
                                        }
                                    }
                                    let batchKeyList = DB.getSchemaComponents(this.chooseData.templateId,DB.selectOne("lists","parent_no="+DB.tableNos['批次'],"").element_type_no)
                                    batchKeyList.forEach((key)=>{
                                        if(key.name == this.chooseData.title){
                                            schema = key
                                            type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,key.type_no)
                                        }
                                    })
                                    break;
                                case "构型":
                                case "技术状态文件":
                                case "有寿件":
                                case "双流水":
                                case "交付遗留问题及处置情况":
                                    isRealList = true
                                    value = DB.Select(this.chooseData.title,'"基线id"='+this.chooseData.baselineId,"")
                                    if(this.chooseData.title == "技术状态文件"){
                                        value = value.filter((item)=>{
                                            return item.state == 0
                                        })
                                    }
                                    type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,DB.tableNos[this.chooseData.title])
                                    value.forEach((item)=>{
                                        for(let key in item){
                                            if(DB.isJSON(item[key])){
                                                item[key] = JSON.parse(item[key])
                                            }
                                        }
                                    })
                                    break;
                                default:
                                    isRealList = true
                                    type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,this.chooseData.table_no)
                                    value = DB.Select(type.name,'"基线id"='+this.chooseData.baselineId+' AND "序号" LIKE \''+this.chooseData.idRule+"%'","")
                                    value.forEach((item)=>{
                                        for(let key in item){
                                            if(DB.isJSON(item[key])){
                                                item[key] = JSON.parse(item[key])
                                            }
                                        }
                                        item['序号'] = item['序号'].replace(this.chooseData.idRule+".","")
                                    })
                                    break;
                            }
                            Bus.$emit("fill-choose",{
                                id:this.chooseData.id,
                                name:this.chooseData.title,
                                relation:{
                                    templateId:this.chooseData.templateId,
                                    baselineId:this.chooseData.baselineId,
                                    configurationId:this.chooseData.configurationId,
                                    stateId:this.chooseData.stateId,
                                    batchId:this.chooseData.batchId,
                                    modelId:this.chooseData.modelId,
                                    tableNo:this.chooseData.table_no,
                                    idRule:this.chooseData.idRule
                                },
                                dataType:this.chooseData.dataType,
                                subType:this.chooseData.title,
                                type:type,
                                value:value,
                                schema:schema,
                                isRealList:isRealList,
                                saved:true
                            },(childList)=>{
                                return DB.processForeignKey(DB.removeUnneedFields(childList,['基线id','id','模板id','工厂id','state',"构型节点id",'previous_version_id','parent_id']))
                            })
                            break;
                        case "type-property":
                            Bus.$emit("design-choose",{
                                type:this.chooseData.type,
                                templateId:this.chooseData.templateId,
                                schema:this.chooseData.schema,
                                name:this.chooseData.title,
                                dataType:this.chooseData.nodeType
                            },'type')
                            break;
                    }
                }
            }else{
                switch (this.routePath){
                    case '/':
                    case '/fill':
                        Bus.$emit("fill-choose",null)
                        break;
                    case '/design':
                        Bus.$emit("design-choose",null)
                    default:
                        if(this.routePath.indexOf('/technical')>=0){
                            Bus.$emit("technical-edit",null)
                        }

                        break;
                }
            }
        },
        append (data,item) {
            if(!data.isCanAppend){
                return
            }
            switch (data.dataType){
                case "selfTypes":
                    const children = data.children || [];
                    let child = {
                        title: item.name,
                        iconType:item.iconType,
                        selected:true,
                        type:item.type,
                        template_id:data.id,
                        isNew:true,
                        isCanEdit:true,
                        isCanDelete:true,
                        dataType:"type"
                    }

                    children.unshift(child);
                    data.selected = false
                    this.$set(data, 'children', children);
                    child.id = DB.Insert('types', {
                        name: child.title,
                        description: "",
                        template_id: child.template_id,
                        type: child.type,
                    })
                    child.no = child.id
                    DB.Update('types',{
                        no:child.id,
                        id:child.no,
                    })
                    this.setChooseData(child)
                    break;
                case "model":
                    const modelList = data.children
                    let newState = {
                        modelId:data.id,
                        templateId:data.templateId,
                        title: "新建状态",
                        iconType:'tree',
                        selected:true,
                        dataType:"state",
                        isNew:true,
                        isCanEdit:true,
//                        isCanDelete:true,
                        isCanAppend:true,
                        isCanPaste:true,
                        children:[]
                    }

                    modelList.unshift(newState);
                    data.selected = false
                    this.$set(data, 'children', modelList);
                    this.$set(data, 'expand', true);
                    newState.id = DB.Insert("状态",{
                        "机型id":data.id,
                        "编号":"新建状态",
                        "名称":"新建状态"
                    })
                    this.setChooseData(newState)
                    break;
                case "state":
                    const stateList = data.children
                    let newBatch = {
                        title: "新建批次",
                        stateId:data.id,
                        modelId:data.modelId,
                        templateId:data.templateId,
                        iconType:'word',
                        selected:true,
                        dataType:"batch",
                        isNew:true,
                        isCanEdit:true,
                        isCanCopy:true,
                        isCanCut:true,
                        isCanDelete:true,
                        isCanAppend:true,
                    }
                    newBatch.id = DB.Insert("批次",{
                        "状态id":data.id,
                        "编号":"新建批次",
                        "名称":"新建批次",
                        "state":"0"
                    })
                    newBatch.batchId = newBatch.id
                    newBatch.baselineId = DB.Insert("基线",{
                        "目标id":newBatch.id,
                        "适用目标":0,
                    })
                    newBatch.configurationId = DB.Insert("构型",{
                        "parent_id":null,
                        "基线id":newBatch.baselineId,
                        "名称":"整机"
                    })
                    newBatch.children = this.createFillChildren(newBatch)
                    stateList.unshift(newBatch);
                    data.selected = false
                    this.$set(data, 'children', stateList);
                    this.$set(data, 'expand', true);
                    this.setChooseData(newBatch)
                    break;
                case "organization":
                    this.modal.showState = true
                    this.modal.viewState = 'air'
                    this.modal.air.viewState = 'organization'
                    this.modal.title = "单机数据包创建"
                    this.updateAirModal()
                    this.modal.air.info.organizationId = data.id
                    break;
                case "batch":
//                    this.modal.showState = true
//                    this.modal.viewState = 'air'
//                    this.modal.air.viewState = 'batch'
//                    this.modal.title = "单机数据包创建"
//
//
//                    this.updateAirModal()
//                    this.modal.air.info.batchId = data.id
                    break;
            }

        },
        remove (root, node, data) {
            switch (data.nodeType){
                case 'supportConfig':
                    this.setChooseData(null)
                    Bus.$emit('design-choose',null)
                    DB.Remove('support_configs',data.id)
                    break;
                case 'batchExtend':
                    this.setChooseData(null)
                    Bus.$emit('design-choose',null)
                    DB.Remove('schemas',data.id)
                    break;
            }
            setTimeout(()=>{
                const parentKey = root.find(el => el === node).parent;
                let index
                if(parentKey!=null){
                    const parent = root.find(el => el.nodeKey === parentKey).node;
                    index = parent.children.indexOf(data);
                    parent.children.splice(index, 1);
                }else{
                    let i = root.findIndex((el)=>{
                        return el === node
                    })
                    this.pageList.forEach((page)=>{
                        index = page.treeList.findIndex((tree)=>{
                            return tree.id == data.id && (tree.dataType?tree.dataType == data.dataType:true)
                        })
                        if(index>=0){
                            page.treeList.splice(index,1)
                        }
                    })
                }
            },200)


        }
    }
}
</script>
