<template>
  <div class="configurationTree">
  <Split v-model="split1" min="0" max="200px">
    <div class="left-box" slot="left">
      <div class="tree-box">
        <Tree  ref="tree" empty-text="" :data="treeList" @on-select-change="nodeClick" :render="renderContent" class="demo-tree-render"></Tree>
      </div>
      <div class="control-box" v-if="!isView">
          <img @click.stop="append()"  src="../../../assets/sidebar/添加@2x.png">
      </div>
    </div>
    <div class="right-box" slot="right">
      <div class="configuration-box" >
        <div class="table-title">
          构型信息
        </div>
        <Table  border  :columns="configurationColumn" :data="configurationInfoList" :disabled-hover="true">
          <template slot-scope="{ row }" slot="control" v-if="row.isCanEdit">
            <img src="../../../assets/users/1@3x.png" class="btn-del" @click.stop="removeCell(row)">
          </template>
          <template slot-scope="{ row }" slot="key">

              <Input :readonly="isView" v-if="row.isCanEdit" v-model="row.key" @on-change="inputConfigChange($event,row)" />
              <span v-else>{{row.key}}</span>
          </template>
          <template slot-scope="{ row }" slot="value">
              <Input v-model="row.value" :readonly="isView"  @on-change="inputConfigChange($event,row)"/>
          </template>
        </Table>
        <div class="append-box" v-if="!isView">
          <img @click.stop="appendKey()"  src="../../../assets/sidebar/添加@2x.png">
        </div>
        <div class="table-title">
          技术状态文件
        </div>
        <Table  border  :disabled-hover="true" :columns="technologyStateFileColumn" :data="technologyStateFileInfoList">
          <template slot-scope="{ row }" slot="control" >
            <img src="../../../assets/users/1@3x.png" class="btn-del" @click.stop="removeFile(row)">
          </template>
          <template slot-scope="{ row }" slot="file">
            <div class="info-file">
              <Input v-model="row.name"  :readonly="isView"  @on-change="inputChange($event,row)"/>
              <div class="info-file-choose" @click="openFile(row)">
                <input v-if="!isView" type="file" id="chooseFile"  @change="fileChange($event,row)">
              </div>
            </div>
          </template>
          <template slot-scope="{ row }" slot="version">
            <Input v-model="row.version" :readonly="isView"  @on-change="inputChange($event,row)"/>
          </template>
          <template slot-scope="{ row }" slot="no">
            <Input v-model="row.no" :readonly="isView"  @on-change="inputChange($event,row)"/>
          </template>
          <template slot-scope="{ row }" slot="type">
            <Select :disabled="isView" v-model="row.typeNo" @on-change="selectChange($event,row)">
              <Option v-for="(item,index) in technologyStateFileTypeList" :value="item.no" :key="index">{{ item.value }}</Option>
            </Select>
          </template>
          <template slot-scope="{ row }" slot="updateTime">
            <DatePicker :readonly="isView" v-model="row.updateTime" @on-change="choseDateChange($event,row)" type="date" placeholder="选择修改日期"></DatePicker>
          </template>

        </Table>
        <div class="append-box" v-if="!isView">
          <img @click.stop="appendFile()"  src="../../../assets/sidebar/添加@2x.png">
        </div>
      </div>
      <div class="control-box" v-if="!isView">
        <img @click="save" src="../../../assets/tree/提交-常态@2x.png">
      </div>


    </div>
 

  </Split>
  </div>
</template>

<script>

import { Tree,Table,Input} from 'view-design';
import Bus from "@event/bus";
import DB from "@lib/Db";
import {dp} from "@lib/util"

export default {
   components: {},

    props: {
      data:{
        type:Object,
        default:{}
      },
        isView:{
          type:Boolean,
            default:false,
        }
    },

    data(){

      return{
        split1: 0.4,
        self:this,
        treeList:[],
        chooseData:null,
        configurationList:[],
        configurationColumn:[
          {
            title:'项目',slot:'key',
            minWidth:120,
          },{
            title:'值',
            slot:'value',
            minWidth:200,
          }
        ],
        configurationInfoList:[],
        technologyStateFileColumn:[

            {
                title:'文件',
                slot:'file',
            },
            {
                title:'版本',
                slot:'version',
            },
            {
                title:'编号',
                slot:'no',
            },
            {
                title:'类型',
                slot:'type',
            },
            {
                title:'修改日期',
                slot:'updateTime',
            },
        ],
        technologyStateFileInfoList:[],
        technologyStateFileTypeList:[],
          technologyStateFileRemoveList:[]
      }
    },
    watch: {
        data(){
            this.load()
        }
    },

    created () {

    },
    beforeDestroy(){
        Bus.$off('configuration-edit-delete')
        Bus.$off('configuration-edit-paste')
        Bus.$off('edit-move')
    },
    mounted () {
        if(!this.isView){
            Bus.$on('configuration-edit-delete',()=>{
                this.removeItem()
            })
            Bus.$on('configuration-edit-copy',()=>{

            })
            Bus.$on('configuration-edit-cut',()=>{

            })
            Bus.$on('configuration-edit-paste',(result)=>{
                switch (result.type){
                    case 0:
//                    let newObject = Object.assign({}, result.data)
//                    delete newObject.node
//                    delete newObject.root
//                    delete newObject.nodeKey
//                    let clean = (node)=>{
//                        delete newObject.node
//                        delete newObject.root
//                        delete newObject.nodeKey
//                        node.children.forEach(()=>{
//
//                        })
//                    }
                        DB.copyConfigurationNode(result.data.id,this.chooseData.id)
                        break;
                    case 1:
                        DB.moveConfigurationNode(result.data.id,this.chooseData.id)
//                    this.chooseData.children.push(result.data)
                        break;
                }
                let index = 0
                if(this.chooseData.children.length){
                    index = DB.Select('构型','id = '+this.chooseData.children[this.chooseData.children.length - 1].id,"")[0]['序号'] + 1
                    DB.Update('构型',{
                        id:result.data.id,
                        '序号':index
                    })
                }
                this.load()
            })
            Bus.$on('edit-move',(type)=>{
                this.move(this.chooseData.root,this.chooseData.node,this.chooseData,type)
            })
            Bus.$on('import-configuration',()=>{
                this.load()
            })
            this.configurationColumn.unshift({
                slot: 'control',
                title:'操作',
                width:80,
                align:'center'
            },)
            this.technologyStateFileColumn.unshift({
                slot: 'control',
                title:'操作',
                width:80,
                align:'center'
            },)
        }
        this.load()
    },
    methods: {
        removeFile(row){
            this.technologyStateFileRemoveList.push(row.id)
            this.technologyStateFileInfoList.splice(row._index,1)
        },
        removeCell(row){
            this.configurationInfoList.splice(row._index,1)
        },
        openFile(row){
            DB.OpenFile(row['附件id'])
        },
      load(){

          this.treeList = []
          this.configurationList = DB.Select("构型",'"基线id" = '+this.data.relation.baselineId,'"序号" ASC')
          let listItem = DB.Select("lists","parent_no="+DB.tableNos['技术状态文件'],"")[0]
          let technologyStateKeyList = DB.getSchemaComponents(this.data.relation.templateId,listItem.element_type_no)
          technologyStateKeyList.forEach((key)=>{
              if(key.name == "类型"){
                  this.technologyStateFileTypeList = DB.getCodeValues(this.data.relation.templateId,key.type_no)
              }
          })
          this.createConfigurationTree()
      },
      removeItem(){
          DB.removeConfigurationNode(this.chooseData.id)
          this.remove(this.chooseData.root,this.chooseData.node,this.chooseData)
      },
      choseDateChange(e,row){
          this.technologyStateFileInfoList[row._index].updateTime = row.updateTime
      },
      selectChange(e,row){
          this.technologyStateFileInfoList[row._index].typeNo = row.typeNo
      },
      inputChange(e,row){
          this.technologyStateFileInfoList[row._index].name = row.name
          this.technologyStateFileInfoList[row._index].version = row.version
          this.technologyStateFileInfoList[row._index].no = row.no
      },
      fileChange(e,row){
        let file = e.target.files[0]
        if(file){
          let file_reader = new FileReader();
          let blob = null
          file_reader.onload = () => {
              let fc = file_reader.result;
              this.technologyStateFileInfoList[row._index].file.content = fc // blob
          };
          file_reader.readAsArrayBuffer(file);
            this.technologyStateFileInfoList[row._index].file = file
            this.technologyStateFileInfoList[row._index].file.ext = file.name.substring(file.name.lastIndexOf(".")+1)
            this.technologyStateFileInfoList[row._index].name = file.name
        }
      },
      inputConfigChange(e,row){
        this.configurationInfoList[row._index].key = row.key
        this.configurationInfoList[row._index].value = row.value
      },
      save(){
        let configurationInfo = {
          id:this.chooseData.id
        }
        let extended = {}
        this.configurationInfoList.forEach((item)=>{
          if(item.isCanEdit&&item.key!=''){
            extended[item.key] = item.value
          }else{
            if(item.key == '名称'){
              this.chooseData.title = item.value
            }
            this.chooseData[item.key] = item.value
            configurationInfo[item.key] = item.value
          }
        })
        configurationInfo.extended = JSON.stringify(extended)
        DB.Update('构型',configurationInfo)
          this.technologyStateFileRemoveList.forEach((id)=>{
            if(id){
                DB.Remove('技术状态文件',id)
            }
          })
          this.technologyStateFileRemoveList = []
        this.technologyStateFileInfoList.forEach((item)=>{
            if(item.id){
              DB.Update('技术状态文件',{
                "名称":item.name,
                "id":item.id,
                "编号":item.no,
                "版本":item.version,
                "修订时间":item.updateTime?item.updateTime.getTime():"",
                "类型":item.typeNo,
              })
              if(item.file){
                  DB.Update('files',{
                      id:item['附件id'],
                      type:item.file.type,
                      ext:item.file.ext
                  })
                  DB.FillFileContent("files",item['附件id'],"content",item.file.content)
              }
            }else{
                item['附件id'] = DB.Insert('files',{
                    type:"技术状态文件"
                })
                item.id = DB.Insert("技术状态文件",{
                "构型节点id":this.chooseData.id,
                "基线id":this.chooseData.baselineId,
                "名称":item.name,
                "编号":item.no,
                "版本":item.version,
                "修订时间":item.updateTime?item.updateTime.getTime():"",
                "类型":item.typeNo,
                "附件": item['附件id'],
                state:0
              })

                if(item.file){
                    DB.Update('files',{
                        id:item['附件id'],
                        type:item.file.type,
                        ext:item.file.ext
                    })
                    DB.FillFileContent("files",item['附件id'],"content",item.file.content)
                }
            }
        })
          this.$Message.success('保存成功！')
      },
      appendFile(){
        this.technologyStateFileInfoList.push({
            name:"",
            no:"",
            version:"",
            typeNo:"",
            updateTime:"",
            file:"",
        })
      },
      appendKey(){
        this.configurationInfoList.push({
          isCanEdit:true,
          key:"",
          value:""
        })
      },
      findConfiguration(configurationId,isRoot){
        let children = []
        let configuration = this.configurationList.find((item)=>{
          return item.id == configurationId
        })
        this.configurationList.forEach((item)=>{
          if(item.parent_id == configurationId){
            children.push(this.findConfiguration(item.id))
          }
        })

        configuration.title = configuration['名称']
        configuration.baselineId = configuration['基线id']
        configuration.parentId = configuration.parent_id
        configuration.dataType = 'configuration'
        if(!isRoot){
          configuration.isCanDelete = true
          configuration.isCanCopy = true
          configuration.isCanCut = true
        }
        configuration.isCanExport = true
        configuration.isCanPaste = true
        configuration.children = children
        if(children.length){
          this.$set(configuration,'expand',true)
        }
        return configuration
      },
      createConfigurationTree(){
        let treeObject = this.findConfiguration(this.data.relation.configurationId,true)
        this.treeList.push(treeObject)
        this.$set(this.treeList[0],'selected',true)
        this.chooseData = this.treeList[0]
        this.chooseCurrentData()
      },
      chooseCurrentData(){
        Bus.$emit("chooseData",this.chooseData)
        this.configurationInfoList = []
        this.technologyStateFileInfoList = []
          this.technologyStateFileRemoveList = []
        let listItem =   DB.selectOne("lists","parent_no="+DB.tableNos['构型'])
        let columnList = DB.construct(this.data.relation.templateId,listItem.element_type_no,DB.selectOne("构型","id = "+this.chooseData.id))
        columnList.forEach((item)=>{
          switch(item.name){
            case 'id':
            case '基线id':
            case 'parent_id':
            case '承制单位id':
            case '序号':
                break;
            case 'extended':
              if(item.value){
                let extendedObject = JSON.parse(item.value)
                for(let key in extendedObject){
                  this.configurationInfoList.push({
                    isCanEdit:true,
                    key:key,
                    value:extendedObject[key]
                  })

                }
              }

              break;
            default:

              this.configurationInfoList.push({
                key:item.name,
                value:item.value?item.value:''
              })
              break;
          }
        })
        let fileList = DB.Select('技术状态文件','"构型节点id" = '+this.chooseData.id,"")

        fileList.forEach((item)=>{
              let file = DB.selectOne('files','id='+item['附件'])
            this.technologyStateFileInfoList.push({
              name:item['名称'],
              no:item['编号'],
              version:item['版本'],
              updateTime:new Date(Number(item['修订时间'])),
              file:file,
              typeNo:item['类型'],
              id:item.id,
                "附件id":item['附件']
            })
        })
      },
      nodeClick(nodeList,node){
          if(nodeList[0]){
            this.chooseData = nodeList[0]
            this.chooseCurrentData()
          }else{
              this.chooseData.selected = true
          }
      },
      renderContent (h, { root, node, data }) {
        data.root = root
        data.node = node
          const parentKey = root.find(el => el === node).parent;
          if(parentKey!=null){
              const parent = root.find(el => el.nodeKey === parentKey).node;
              let index = parent.children.findIndex((el)=>el.nodeKey === node.nodeKey)
              data.isCanMoveUp = index!=0
              data.isCanMoveDown = index != parent.children.length - 1
          }
        return h('span', {
          style: {
            display: 'inline-block',
            width: '100%',
            // opacity:data.isNew?'0.5':'1'
          }
        }, [
          h('span', [
    
            h('span', data.title)
          ])
        ]);
      },
      append () {
        let data = this.chooseData
        if(data){
            const children = data.children || [];
            let child = {
              title: "新建构型",
              baselineId:data.baselineId,
              parentId:data.id,
              selected:true,
              isNew:true,
              isCanDelete:true,
              isCanCopy:true,
              isCanCut:true,
              isCanPaste:true,
              isCanMove:true,
              dataType:'configuration',
              children:[]
            }
            this.chooseData = child
            let index = 0
            if(children.length){
                index = DB.Select('构型','id = '+children[children.length - 1].id,"")[0]['序号'] + 1
            }
            this.chooseData.id = DB.Insert('构型', {
              "基线id":this.chooseData.baselineId,
              parent_id:this.chooseData.parentId,
              "编号":"",
              "名称": this.chooseData.title,
              "备注": "",
              "序号": index,
              extended: ""
            })
            children.push(child);
            data.selected = false
            this.$set(data, 'expand', true);
            this.$set(data, 'children', children);
            this.$nextTick(()=>{
                this.chooseCurrentData()
            })
        }
      },
      move (root, node, data ,type) {
          const parentKey = root.find(el => el === node).parent;
          let index
          let otherIndex
          if(parentKey!=null){
              const parent = root.find(el => el.nodeKey === parentKey).node;
              index = parent.children.findIndex((el)=>el.nodeKey === data.nodeKey);
              parent.children[index].selected = false
              if(type == 'up'){
                  otherIndex = index - 1
              }else{
                  otherIndex = index + 1
              }
              parent.children.splice(otherIndex,1,...parent.children.splice(index,1,parent.children[otherIndex]))
              this.$set(parent.children[otherIndex],'selected',true)
              this.chooseData = parent.children[otherIndex]
              /*互换序号*/
              let newIndex = DB.Select('构型','id = '+parent.children[index].id,'')[0]['序号']
              let oldIndex = DB.Select('构型','id = '+parent.children[otherIndex].id,'')[0]['序号']
              DB.Update("构型",{
                  id:parent.children[otherIndex].id,
                  "序号":newIndex
              })
              DB.Update("构型",{
                  id:parent.children[index].id,
                  "序号":oldIndex
              })

              this.$nextTick(()=>{
                  this.chooseCurrentData()
              })
          }
      },
      remove (root, node, data ) {
        const parentKey = root.find(el => el.nodeKey === node.nodeKey).parent;
        let index
        if(parentKey!=null){
          const parent = root.find(el => el.nodeKey === parentKey).node;
          index = parent.children.findIndex(el => el.nodeKey === node.nodeKey);
            parent.children.splice(index, 1);
            if(!parent.children.length){
                this.$set(parent,'expand',false)
            }
            this.$refs.tree.handleSelect(parent.nodeKey)
        }
      }
    }
}
</script>
<style >
