<template>
  <div id="basisData">
    <Split v-model="split1" min="0" max="200px">
      <Sider slot="left"/>
      <div class="right-box" slot="right">
        <div class="table-box" v-show="title">
          <div class="table-title">
            {{title}}
            <img src="../../assets/users/提交-常态@3x.png" @click="save">
          </div>
          <Table  border  :columns="columns" :data="content" :max-height="clientHeight * 0.78" :disabled-hover="true">
            <template slot-scope="{ row }" slot="controls">
              <img src="../../assets/users/1@3x.png" class="btn-del" @click="remove(row)">
            </template>
            <template slot-scope="{ row }" slot="no">
              <Input v-model="row.no"  @on-change="inputChange($event,row)"/>
            </template>
            <template slot-scope="{ row }" slot="name">
              <Input v-model="row.name"  @on-change="inputChange($event,row)"/>
            </template>
            <template slot-scope="{ row }" slot="address">
              <Input v-model="row.address"  @on-change="inputChange($event,row)"/>
            </template>
            <template slot-scope="{ row }" slot="person">
              <Input v-model="row.person"  @on-change="inputChange($event,row)"/>
            </template>
            <template slot-scope="{ row }" slot="telephone">
              <Input v-model="row.telephone"  @on-change="inputChange($event,row)"/>
            </template>
            <template slot-scope="{ row }" slot="managerName">
              <Input v-model="row.managerName"  @on-change="inputChange($event,row)"/>
            </template>
            <template slot-scope="{ row }" slot="managerPassword">
              <Input v-model="row.managerPassword"  @on-change="inputChange($event,row)"/>
            </template>
          </Table>
          <div class="append-box">
            <img @click.stop="appendKey()"  src="../../assets/sidebar/添加@2x.png">
          </div>
        </div>
      </div>
    </Split>
  </div>
</template>

<script>
import DB from "@lib/Db";
import Sider from '@component/Sider'
import Bus from '@event/bus';
export default {
    components: {
        Sider,
    },

    props: {

    },

    data(){
        return{
            clientHeight:document.documentElement.clientHeight,
            split1:0.15,
            title:'',
            columns:[

                {
                    slot:'controls',
                    title:'操作',
                    width:80,
                    align: 'center',

                },                {
                    align: 'center',
                    title:'编号',
                    slot:"no"
                },{
                    slot:'name',
                    title:'单位名称',
                    align: 'center',

                }
                ,{
                    slot:'address',
                    title:'单位地址',
                    align: 'center',

                },{
                    slot:'person',
                    title:'联系人',
                    align: 'center',

                }
                ,{
                    slot:'telephone',
                    title:'电话',
                    align: 'center',
                }
                ,{
                    slot:'managerName',
                    title:'管理员用户名',
                    align: 'center',
                },{
                    slot:'managerPassword',
                    title:'管理员密码',
                    align: 'center',
                }
            ],
            content:[],
            organizationList:[],
            organizationType:0
        }
    },

    watch: {},

    created () {},
    beforeDestroy(){
        Bus.$off('basisData-choose')
    },
    mounted () {
        Bus.$on("basisData-choose",(data)=>{
            let content = []
            switch (data.name){
                case "承制单位":
                    this.title = data.name
                    this.organizationType = 1
                    this.processOrganizationList(content)
                    break;
                case "接装单位":
                    this.title = data.name
                    this.organizationType = 2
                    this.processOrganizationList(content)
                    break;
                default:
                    this.title = ""
                    break;

            }
        })
    },

    methods: {
        processOrganizationList(){
            this.organizationList = DB.Select('组织','"类型"='+this.organizationType,'')
            this.userList = DB.Select("用户","","")
            let list = []
            this.organizationList.forEach((item)=>{
                if(item['类型'] == this.organizationType){
                    list.push(item)
                }
            })
            list.forEach((item)=>{
                item.no = item['编号']
                item.name = item['名称']
                item.address = item['地址']
                item.telephone = item['电话']
                item.person = item['联系人']
                let user = this.userList.find((u)=>{
                    return u.id == item['管理员id']
                })
                if(user){
                    item.managerName = user['登录名']
                    item.managerPassword = user['密码']
                }
            })
            this.content = list
        },
        remove(row){
            this.content.splice(row._index,1)
        },
        save(){
            let isHave = false
            this.content.forEach((item)=>{
                let user = DB.selectOne('用户','"登录名" = \''+item.managerName+"'")
                if(!user||(user&&item.id==user.id)){
                    if(item.id){
                        if(item['管理员id']){
                            DB.Update("用户",{
                                id:item['管理员id'],
                                "登录名":item.managerName,
                                "密码":item.managerPassword,
                            })
                        }else{
                            item['管理员id'] = DB.Insert("用户",{
                                "登录名":item.managerName,
                                role:"管理员",
                                "密码":item.managerPassword,
                                "组织id":item.id
                            })
                        }
                        DB.Update("组织",{
                            id:item.id,
                            "编号":item.no,
                            "名称":item.name,
                            "地址":item.address,
                            "电话":item.telephone,
                            "联系人":item.person,
                            "管理员id":item['管理员id']
                        })
                    }else{
                        item['管理员id'] = DB.Insert("用户",{
                            "登录名":item.managerName,
                            role:"管理员",
                            "密码":item.managerPassword,
                        })
                        item.id = DB.Insert("组织",{
                            "编号":item.no,
                            "名称":item.name,
                            "地址":item.address,
                            "电话":item.telephone,
                            "联系人":item.person,
                            "类型":this.organizationType,
                            "管理员id":item['管理员id']
                        })
                        DB.Update("用户",{
                            "组织id":item.id,
                            id:item['管理员id']
                        })
                    }
                }else{
                    isHave = true
                }
            })
            this.organizationList.forEach((item)=>{
                let organization = this.content.find((c)=>{
                    return c.id == item.id
                })
                if(!organization){
                    DB.Remove('组织',item.id)
                }
            })
            if(isHave){
                this.$Message.error("管理员重复命名的，无法保存！")
            }else{
                this.$Message.success("保存成功！")
            }
        },
        inputChange(e,row){
            this.content[row._index] = row
        },
        appendKey(){

            this.content.push({
                no:"",
                name:"",
                address:"",
                telephone:"",
                person:""
            })
            this.$nextTick(()=>{
                let domList = document.getElementsByClassName("ivu-table-overflowY")
                if(domList.length){
                    domList[0].scrollTop = domList[0].scrollHeight
                }
            })

        }
    }
}
</script>

<style>
  #basisData .table-box{
    width: 95%;
    margin: auto;
  }
  #basisData .table-title{
    border:solid 1px #dcdee2;
    margin-top: 20px;
    height: 50px;
    line-height: 50px;
    border-bottom: none;
    text-align: center;
    background: rgb(225,241,249);
    position: relative;
  }
  #basisData .table-title img{
    position: absolute;
    right: 20px;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 30px;
  }
  #basisData .btn-del{
    width: 20px;
    display: block;
    margin: auto;
  }
  #basisData .append-box{
    border:solid 1px #dcdee2;
    border-top: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
  }
  #basisData .append-box img{
    height: 50%;
  }
  #basisData .ivu-table-wrapper{
    overflow: visible;
  }
</style>