<template>
  <div id="technicalMain">
    <div class="table-box">
      <div class="table-title">
        技术状态控制
      </div>
      <Table  border  :columns="columns" :data="content" :max-height="clientHeight * 0.78">
          <template slot-scope="{ row }" slot="time">
              {{DatetimeToStr(row.time)}}
          </template>
        <template slot-scope="{ row }" slot="name">
          {{row.name}}
        </template>
        <template slot-scope="{ row }" slot="baseline">
          <img src="../../assets/tree/文件选择图标@3x.png" class="detail-btn" @click="intoDetail(row.id,'detail')">
        </template>
        <template slot-scope="{ row }" slot="change">
          <img src="../../assets/tree/文件选择图标@3x.png" class="detail-btn" @click="intoDetail(row.id,'maintain')">

        </template>
      </Table>
    </div>
    <div class="append-box">
      <img @click.stop="append()"  src="../../assets/sidebar/添加@2x.png">
    </div>
      <Modal
              v-model="modal.showState"
              class-name="modal"
              :closable="false"
              :mask-closable="false"
              :styles="{top: '0',width:'420px'}"
      >
          <div slot="header" class="modal-header">
              <div class="title">{{modal.title}}</div>
              <div class="control">

                  <img src="../../assets/sidebar/关闭1@3x.png" @click="cancel()"><img src="../../assets/sidebar/确认1@3x.png" @click="confirm()">
              </div>
          </div>
          <div class="control-info" v-show="modal.viewState == 'model'">
              <Form :model="modal.info"  ref="formValidate" :rules="ruleValidate" :label-width="100">
                  <FormItem label="文件" prop="name">
                      <div class="info-file">
                          <Input v-model="modal.info.name"></Input>
                          <div class="info-file-choose">
                              <input type="file" id="chooseFile"  @change="fileChange($event)">
                          </div>
                      </div>

                  </FormItem>
                  <FormItem label="编号" prop="no">
                      <Input v-model="modal.info.no"></Input>
                  </FormItem>
                  <!--<FormItem label="版本" prop="version">-->
                      <!--<Input v-model="modal.info.version"></Input>-->
                  <!--</FormItem>-->
                  <FormItem label="备注">
                      <Input v-model="modal.info.description"></Input>
                  </FormItem>
              </Form>
          </div>
          <div slot="footer" class="modal-footer">
              <Button  size="large"  @click="confirm">下一步</Button>
          </div>
      </Modal>
  </div>
</template>

<script>
import Bus from '@event/bus';
import DB from '@lib/Db';
import {dp} from '@lib/util'
export default {
    data() {
        return {
            clientHeight:document.documentElement.clientHeight,

            columns:[
                {
                    type: 'index',
                    align: 'center',
                    title:'编号',
                    width:80
                },
                {
                    slot:'time',
                    title:'时间',
                    align: 'center',

                }
                ,{
                    slot:'name',
                    title:'变更文件名称',
                    align: 'center',

                },{
                    slot:'baseline',
                    title:'基线数据',
                    align: 'center',

                }
                ,{
                    slot:'change',
                    title:'变更内容',
                    align: 'center',
                },
                {
                    key:'description',
                    title:'备注',
                    align:'center'
                }
            ],
            content:[],
            modal:{
                showState:false,
                viewState:'model',//organization  air  model template
                title:"新增变更文件",
                info:{
                    name:"",
                    version:"",
                    no:"",
                    file:"",
                    description:"",
                    fileName:""
                }
            },
            ruleValidate: {
                name: [
                    {
                        required: true,
                        message: '文件不能为空',
                        trigger: 'blur'
                    }
                ],
                no: [
                    { required: true, message: '编号不能为空', trigger: 'blur' },
                ]
            }
        }
    },
    methods:{
        NumToStr(num){
            if(num<10){
                num = "0"+num
            }
            return num
        },
        DatetimeToStr(datetime){
            let date = new Date(Number(datetime))
            return date.getFullYear()+"-"+this.NumToStr((date.getMonth()+1))+"-"+this.NumToStr(date.getDate())+"  "+this.NumToStr(date.getHours())+":"+this.NumToStr(date.getMinutes())
        },
        confirm(){
            this.$refs.formValidate.validate((valid) => {
                if(valid){
                    let date = Date.now()
                    let id = DB.Insert('files',{
                        type:"技术状态文件"
                    })
                    let fileId = DB.Insert("技术状态文件",{
                        "构型节点id":this.$route.query.configurationId,
                        "基线id":this.$route.query.baselineId,
                        "名称":this.modal.info.name,
                        "路径":this.modal.info.fileName,
//                        "版本":this.modal.info.version,
                        "修订时间":date,
                        "类型":11,
                        "state":0,
                        "附件":id
                    })
                    DB.Update("技术状态文件",{
                        "编号":fileId,
                    })
                    if(this.modal.info.file){
                        DB.Update("files",{
                            id:id,
                            type:this.modal.info.file.type,
                            ext:this.modal.info.file.ext
                        })
                        DB.FillFileContent("files",id,"content",this.modal.info.file.content)
                    }
                    let historyId = DB.Insert("change_histories",{
                        "基线id":this.$route.query.baselineId,
                        "time":date,
                        "control_file_id":fileId,
                        "no":this.modal.info.no,
                        "description":this.modal.info.description
                    })
                    this.modal.showState = false
                    this.intoDetail(historyId,'detail',true)
                }
            })
        },
        cancel(){
            this.modal.showState = false
        },
        fileChange(e){
            let file = e.target.files[0]
            if(file){
                let file_reader = new FileReader();
                let blob = null
                file_reader.onload = () => {
                    let fc = file_reader.result;
//                    blob = new Blob([fc])
                    this.modal.info.file.content = fc
                };
                file_reader.readAsArrayBuffer(file);
                this.modal.info.file = file
                this.modal.info.file.ext = file.name.substring(file.name.lastIndexOf(".")+1)
                this.modal.info.name = file.name
                this.modal.info.fileName = file.name
            }
        },
        intoDetail(changeHistoryId,path,isEdit){
            let query = {
                configurationId:this.$route.query.configurationId,
                baselineId:this.$route.query.baselineId,
                changeHistoryId:changeHistoryId,
                templateId:this.$route.query.templateId,
            }
            if(isEdit){
                query.isEdit = true
            }
            this.$router.push({
                path:'/technical/'+path,
                query:query
            })
        },
        append(){
            this.modal.showState = true
        }
    },
    mounted(){
        this.configuration = DB.selectOne("构型","id="+this.$route.query.configurationId,"")
        let fileList = DB.Select("技术状态文件",'"基线id" = '+this.$route.query.baselineId,"")
        let content = DB.Select("change_histories",'"基线id" = '+this.$route.query.baselineId,"")
        content.forEach((item)=>{
            let file = DB.selectOne("技术状态文件","id="+item.control_file_id,"")
            item.name = file["名称"]
        })
        this.content = content
    },
    components: {

    },
}
</script>

<style>
  #technicalMain{
    display: flex;
    justify-content: center;
    position: relative;
    width: 100%;
    height: 100%;
  }
  #technicalMain .table-box{
    width: 1600px;

  }
  #technicalMain .table-title{
    border:solid 1px #dcdee2;
    margin-top: 20px;
    height: 50px;
    line-height: 50px;
    border-bottom: none;
    text-align: center;
    background: rgb(225,241,249);
    position: relative;
  }
  #technicalMain .detail-btn{
    width:15px;
    display: block;
    margin: auto;
  }
  #technicalMain .append-box{
    height: 5%;
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    border-top:solid 1px rgb(225,225,225);
  }
  #technicalMain .append-box img{
    height: 40%;
  }
</style>