<template>
  <div class="composite">
    <Table row-key="id" border :disabled-hover="true" :columns="columns" :data="content" >
      <template slot-scope="{ row }" slot="name">
        <span style="padding-left: 20px;" v-if="!row.children||!row.children.length"><span style="color: #FF0000;margin-right: 5px;" v-if="row.schema.is_required == 1">※</span></span>{{row.title}}
      </template>
      <template slot-scope="{ row }" slot="value">
        <div style="padding-top: 10px;padding-bottom: 10px">
          <Item :isView="isView" :ref="'ref-'+row.schema.id"  v-if="row.type.type<=1||row.valueSet" v-model="row" :control="{row:5}"></Item>
          <TableList :isView="isView"  :ref="'ref-'+row.schema.id"  :columns="row.columns" :item="row"  :expandColumns="row.expandColumns" :content="row.value" v-if="row.type.type==3&&!row.valueSet"></TableList>
        </div>
      </template>
    </Table>
  </div>
</template>

<script>
import DB from "@lib/Db";
import Bus from '@event/bus';
import {dp} from '@lib/util'
import Item from '@component/fill/Item'
import TableList from '@component/fill/TableList'

export default {
    components: {
        Item,
        TableList
    },

    props: {
        content:{
            type:Array,
            default(){
                return []
            },
        },
        isView:{
            type:Boolean,
            default:false
        }
    },

    data(){
        return{
            columns:[{
                title:"项目",
                slot:"name",
                tree:true,
                display:'inline',
                resizable: true,
                width: 200,
            },{
                title:"值",
                slot:'value'
            }],
        }
    },

    watch: {

    },

    created () {},
    beforeDestroy(){

    },
    mounted () {


    },

    methods: {
        getRefValue(refName,value){
            let ref = null
            if(this.$refs[refName]&&this.$refs[refName][0]){
                ref = this.$refs[refName][0]
            }else{
                ref = this.$refs[refName]
            }
            if(ref&&ref.getValue){
                let obj = ref.getValue()
                if(!obj.state){
                    this.state = obj.state
                }
                value = obj.value
            }
            return value
        },
        getValue(){
            this.state = true
            let value = {

            }
            this.content.forEach((item)=>{
                this.findValue(item,value)
            })
            return {
                state:this.state,
                value:value
            }
        },
        findValue(item,value){
            let childObj
            switch (item.type.type){
                case 2:
                    childObj = value[item.name] = {}
                    break;
                default:
                    value[item.name] = this.getRefValue('ref-'+item.schema.id,null)
                    break;
            }
            item.children.forEach((child)=>{
                this.findValue(child,childObj)
            })
        }
    }
}
</script>

